import React, { useState, useEffect } from 'react';
import { useAuthContext } from '../../Context/AuthContext';
import TestDashboard from '.././Candidate/Components/TestDashboard'; // Import your TestDashboard component

const CandidateHomePage = () => {
  const { authState } = useAuthContext();
  const [candidatePaperSets, setCandidatePaperSets] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [selectedPaperSet, setSelectedPaperSet] = useState(null); // New state for the selected paper set

  // Fetch paper sets
  useEffect(() => {
    const fetchCandidatePaperSets = async () => {
      try {
        const response = await fetch(`https://localhost:5201/CandidatePaperSet/GetCandidatePaperSets/${authState.userId}`, {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${authState.token}`,
          },
        });

        if (response.ok) {
          const data = await response.json();
          setCandidatePaperSets(data);
        } else {
          setError('Failed to fetch paper sets');
        }
      } catch (error) {
        setError('Error fetching data');
      } finally {
        setLoading(false);
      }
    };

    if (authState.userId && authState.token) {
      fetchCandidatePaperSets();
    }
  }, [authState.userId, authState.token]);

  const handleStartTest = (paperSet) => {
    setSelectedPaperSet(paperSet); // Set the selected paper set to the state
  };

  if (loading) {
    return <div className="text-center text-lg mt-5">Loading...</div>;
  }

  if (error) {
    return <div className="text-center text-lg mt-5">{error}</div>;
  }

  return (
    <div className="p-5">
      {/* If a paper set is selected, render TestDashboard */}
      {selectedPaperSet ? (
        <TestDashboard paperSet={selectedPaperSet} /> // Pass the selected paper set to the TestDashboard component
      ) : (
        <>
          {/* Paper Sets Section */}
          <h2 className="text-3xl font-bold underline text-center mb-8">Assigned Tests</h2>
          <table className="w-full table-auto border-collapse mb-5">
            <thead>
              <tr className="bg-gray-200 text-lg font-bold">
                <th className="px-4 py-2 border border-gray-300 text-left">Paper Set Name</th>
                <th className="px-4 py-2 border border-gray-300 text-left">Status</th>
                <th className="px-4 py-2 border border-gray-300 text-left">TotalQuestion</th>
                <th className="px-4 py-2 border border-gray-300 text-left">Time</th>
                <th className="px-4 py-2 border border-gray-300 text-left">Actions</th>
              </tr>
            </thead>
            <tbody>
              {candidatePaperSets.map((paperSet, index) => (
                <tr key={index} className="odd:bg-gray-50 even:bg-gray-200">
                  <td className="px-4 py-2 border border-gray-300 text-left">{paperSet.paperSetName}</td>
                  <td className="px-4 py-2 border border-gray-300 text-left">
                    <span className={paperSet.status ? 'text-green-500 font-bold' : 'text-orange-500 font-bold'}>
                      {paperSet.status ? 'Attempted' : 'Not Attempted'}
                    </span>
                  </td>
                  <td className="px-4 py-2 border border-gray-300 text-left">{paperSet.totalQuestions}</td>
                  <td className="px-4 py-2 border border-gray-300 text-left">{paperSet.timeInMinutes}</td>
                  <td className="px-4 py-2 border border-gray-300 text-left">
                    {!paperSet.status && (
                      <button
                        className="bg-blue-500 text-white px-4 py-1 rounded hover:bg-blue-600 transition"
                        onClick={() => handleStartTest(paperSet)} // Pass the whole paperSet
                      >
                        Start
                      </button>
                    )}
                    {paperSet.status === 'InProgress' && (
                      <button
                        className="bg-yellow-500 text-white px-4 py-1 rounded hover:bg-yellow-600 transition"
                        onClick={() => handleStartTest(paperSet)}
                      >
                        Resume
                      </button>
                    )}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </>
      )}
    </div>
  );
};

export default CandidateHomePage;
