import React, { useState, useEffect } from 'react';
import { useAuthContext } from '../../../Context/AuthContext';
import '../../../assets/styles/style.css';
import QuestionDashboard from './QuestionDashboard'; // Import the QuestionDashboard component
import { showConfirmationDialog } from '../../../Components/PopupConfirmations';

const TestDashboard = ({ paperSet, progress }) => {
    const { authState } = useAuthContext();
    const [isTestStarted, setIsTestStarted] = useState(false);  // State to track if test has started
    const [error, setError] = useState(null);  // State to track errors
    const [timer, setTimer] = useState(null);  // State to track the test timer
    const [currentPage, setCurrentPage] = useState(0); // State to track

    // Log the paperSet to see what data is coming in
    console.log(paperSet);

    const handleStartTest = async () => {
    
        const isConfirmed = await showConfirmationDialog("Do you want to start the test?");
        if (isConfirmed) {
            // Prepare the DTO (Data Transfer Object) for the request
            const startExamDTO = {
                CandidateId: authState.userId,
                PaperSetId: paperSet.paperSetId,
                PaperSetName: paperSet.paperSetName,
                CreatedBy_UserId: authState.userId,
                UpdatedBy_UserId: authState.userId,
            };
    
            try {
                console.log("Sending start exam request:", startExamDTO); // Log DTO before sending request
    
                // Make the API request to start the exam
                const response = await fetch('https://localhost:5201/CandidatePaperSet/StartExam', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Bearer ${authState.token}`,
                    },
                    body: JSON.stringify(startExamDTO),
                });
    
                // Log the response status and body
                console.log("Response status:", response.status);
                const responseData = await response.json();
                console.log("Response data:", responseData);
    
                // Check if the response is successful
                if (response.ok) {
                    setIsTestStarted(true);  // Set the test as started
                    setError(null); // Clear any previous errors
                } else {
                    // Handle errors if response is not OK
                    console.error("Error starting the exam:", responseData);
                    setError(responseData.message || "An error occurred while starting the exam.");
                }
            } catch (error) {
                // Handle any network or unexpected errors
                console.error("Error during the fetch request:", error);
                setError('An error occurred while starting the exam. Please try again later.');
            }
        }
    };
    useEffect(() => {
        if (progress) {
          setTimer(progress.remainingTime);
          setCurrentPage(progress.currentQuestionIndex);
        }
      }, [progress]);
    

    // If test is started, render QuestionDashboard, else render TestDashboard
    if (isTestStarted) {
        return <QuestionDashboard paperSetId={paperSet.paperSetId} paperSetName={paperSet.paperSetName} />;
    }
    

    return (
        <div className="dashboard-container">
            <div className="content-layout">
                {/* Left Column: Test Details */}
                <div className="left-column flex flex-col justify-between">
                    {/* Header Section */}
                    <div className="header">
                        <h1>
                            Welcome to <br /> <span>{paperSet.paperSetName}</span> Test
                        </h1>
                        <div className="stats">
                            <p>Question count: <strong>{paperSet.totalQuestions}</strong></p>
                            <p>Test Duration: <strong>{paperSet.timeInMinutes} Minutes</strong></p>
                        </div>
                    </div>

                    <div className="button-container flex justify-end mt-4">
                        <button
                            type="submit"
                            onClick={handleStartTest} // Handle the button click
                            className="py-3 px-6 text-base font-semibold text-white bg-indigo-600 hover:bg-indigo-700 rounded-lg shadow focus:ring-2 focus:ring-indigo-500 focus:outline-none"
                        >
                            Start Test
                        </button>
                    </div>
                </div>

                {/* Right Column: Instructions */}
                <div className="right-column">
                    <h2>Instructions for the test</h2>
                    <ul className="instructions-list">
                        <li>
                            Please review the exam guidelines carefully before beginning. Make sure you understand all the rules.
                        </li>
                        <li>
                            Have your unique student ID ready. You will need this to start the exam. If you've misplaced it, check your registration email or contact support at <a href="mailto:support@examplatform.com">support@examplatform.com</a>.
                        </li>
                        <li>
                            Once you enter your ID, verify your personal information (name and class) on the screen to ensure it is accurate.
                        </li>
                        <li>
                            For the best experience, take the exam on a desktop or laptop. Avoid using mobile phones or tablets.
                        </li>
                        <li>
                            Ensure your webcam and microphone are working correctly if you're using a desktop or laptop, as they may be required for identity verification.
                        </li>
                    </ul>
                </div>

                {/* Error Handling */}
                {error && (
                    <div className="error-message" style={{ color: 'red', marginTop: '20px' }}>
                        <p>{error}</p>
                    </div>
                )}
            </div>
        </div>
    );
};

export default TestDashboard;
