import React, { useState, useEffect } from 'react';
import { useAuthContext } from '../../../Context/AuthContext';
import EditCandidateProfile from './EditCandidateProfile';
import { showSuccessNotification} from '../../../Components/PopupConfirmations';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPenToSquare } from '@fortawesome/free-solid-svg-icons';

const CandidateProfile = () => {
    const { authState } = useAuthContext();
    const [candidate, setCandidate] = useState(null);
    const [isPopupOpen, setPopupOpen] = useState(false);
    const [isCurrentContentVisible, setCurrentContentVisibility] = useState(true);
    const [resumeFile, setResumeFile] = useState(null);

    const handleOpenPopup = () => {
        setPopupOpen(true);
        setCurrentContentVisibility(false);
    };

    const handleClosePopup = () => {
        setPopupOpen(false);
        setCurrentContentVisibility(true);
    };

    useEffect(() => {
        const fetchCandidateData = async () => {
            try {
                const response = await fetch(`https://localhost:5201/Candidate/Id?id=${authState.userId}`, {
                    headers: {
                        'Authorization': `Bearer ${authState.token}`,
                    },
                });
                if (response.ok) {
                    const data = await response.json();
                    setCandidate(data); 
                } else {
                    console.error('Failed to fetch candidate data:', response.statusText);
                }
            } catch (error) {
                console.error('Error fetching candidate data:', error);
            }
        };

        if (authState.isAuthenticated) {
            fetchCandidateData(); // Single API call
        }
    }, [authState.isAuthenticated, authState.token, authState.userId]);

    const handleFileChange = (event) => {
        setResumeFile(event.target.files[0]);
    };

    const handleUpload = async () => {
        if (!resumeFile) {
            console.error('Resume file is required.');
            return;
        }
        try {
            const formData = new FormData();
            formData.append('resumeFile', resumeFile);

            const response = await fetch(`https://localhost:5201/Candidate/AddCandidateResume?id=${authState.userId}`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${authState.token}`,
                },
                body: formData,
            });

            if (response.ok) {
                await showSuccessNotification("Resume uploaded successfully");
            } else {
                console.error('Failed to upload resume:', response.statusText);
            }
        } catch (error) {
            console.error('Error uploading resume:', error);
        }
    };

    const handleResumeLinkClick = () => {
        if (candidate && candidate.imageUrl) {
            const imageUrl = `https://localhost:5201${candidate.imageUrl}`;
            window.open(imageUrl, '_blank');
        }
    };

    return (
        <div className="bg-white min-h-screen py-10 px-5">
            {candidate && isCurrentContentVisible && (
                <div className="max-w-3xl mx-auto bg-gray-100 shadow-xl rounded-xl overflow-hidden">
                    <div className="flex justify-between items-center bg-gray-200 text-black p-6">
                        <div>
                            <h2 className="text-3xl font-semibold">{candidate.fullName}</h2>
                            <p className="text-lg">{candidate.emailAddress}</p>
                        </div>
                        <div className="flex items-center">
                            <div className="w-24 h-24 rounded-full overflow-hidden border-4 border-white shadow-md bg-gray-100">
                                {candidate.imageUrl ? (
                                    <img
                                        src={`https://localhost:5201${candidate.imageUrl}`}
                                        alt="Candidate"
                                        className="w-full h-full object-cover"
                                    />
                                ) : (
                                    <svg
                                        className="w-full h-full text-gray-400"
                                        fill="currentColor"
                                        viewBox="0 0 20 20"
                                        xmlns="http://www.w3.org/2000/svg"
                                    >
                                        <path
                                            fillRule="evenodd"
                                            d="M10 9a3 3 0 100-6 3 3 0 000 6zm-7 9a7 7 0 1114 0H3z"
                                            clipRule="evenodd"
                                        ></path>
                                    </svg>
                                )}
                            </div>
                        </div>
                    </div>

                    <div className="p-6 bg-gray-100">
                        <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                            <div className="bg-white p-4 rounded-lg shadow-md">
                                <div className="flex justify-between">
                                    <h3 className="text-xl font-semibold text-gray-800 mb-3">Personal Details</h3>
                                    <button
                                        onClick={handleOpenPopup}
                                        className="px-2 bg-gray-500 text-white rounded-md text-lg hover:bg-gray-600"
                                    >
                                        <FontAwesomeIcon icon={faPenToSquare} />
                                    </button>
                                </div>
                                <div className="space-y-2 text-left">
                                    <p><strong>First Name:</strong> {candidate.firstName}</p>
                                    <p><strong>Middle Name:</strong> {candidate.middleName}</p>
                                    <p><strong>Last Name:</strong> {candidate.lastName}</p>
                                    <p><strong>Mobile Number:</strong> {candidate.mobileNumber}</p>
                                    <p><strong>Email:</strong> {candidate.emailAddress}</p>
                                    <p><strong>Pan Number:</strong> {candidate.pan}</p>
                                </div>
                            </div>

                            <div className="bg-white p-4 rounded-lg shadow-md">
                                <div className="flex justify-between">
                                    <h3 className="text-xl font-semibold text-gray-800 mb-3">Address</h3>
                                    <button
                                        onClick={handleOpenPopup}
                                        className="px-2 bg-gray-500 text-white rounded-md text-lg hover:bg-gray-600"
                                    >
                                        <FontAwesomeIcon icon={faPenToSquare} />
                                    </button>
                                </div>
                                <div className="space-y-4 text-left">
                                    <div>
                                        <p><strong>Permanent Address:</strong></p>
                                        <p>{candidate.permanentAddress?.addressLine}, {candidate.permanentAddress?.city}</p>
                                    </div>
                                    <div>
                                        <p><strong>Current Address:</strong></p>
                                        <p>{candidate.currentAddress?.addressLine}, {candidate.currentAddress?.city}</p>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="bg-white p-4 rounded-lg shadow-md mt-6">
                            <h3 className="text-xl font-semibold text-gray-800 mb-3">Resume</h3>
                            <div className="flex flex-col sm:flex-row justify-between items-center gap-4">
                                {!candidate.imageUrl && (
                                    <>
                                        <input
                                            type="file"
                                            onChange={handleFileChange}
                                            className="border border-gray-300 rounded px-3 py-2 text-sm w-full sm:w-auto"
                                        />
                                        <button
                                            onClick={handleUpload}
                                            className="bg-blue-500 text-white px-6 py-2 rounded-md text-sm hover:bg-blue-600 focus:ring-2 focus:ring-blue-300 focus:outline-none transition-all w-full sm:w-auto"
                                        >
                                            Upload Resume
                                        </button>
                                    </>
                                )}
                                {candidate.imageUrl && (
                                    <>
                                        <img
                                            src={`https://localhost:5201${candidate.imageUrl}`}
                                            alt={`Resume of ${candidate.firstName}`}
                                            className="h-12 w-12 sm:h-10 sm:w-10 rounded-full object-cover"
                                        />
                                        <button
                                            onClick={handleResumeLinkClick}
                                            className="text-blue-600 text-sm underline hover:text-blue-800"
                                        >
                                            View Resume
                                        </button>
                                    </>
                                )}
                            </div>
                        </div>

                    </div>
                </div>
            )}

            {isPopupOpen && (
                <div className="bg-gray-200 mx-auto rounded-xl p-8 shadow-lg w-3/4 lg:w-1/2">
                    <EditCandidateProfile id={candidate?.id} token={authState.token} onClose={handleClosePopup} />
                </div>
            )}
        </div>
    );
};

export default CandidateProfile;
