import React, { useState, useEffect, useCallback } from 'react';
import { useAuthContext } from '../../../Context/AuthContext';

const PaperSetView = ({ id, name, onClose }) => {
  const { authState } = useAuthContext();
  const [questions, setQuestions] = useState([]);
  const [notQuestions, setNotQuestions] = useState([]);
  const [selectedQuestions, setSelectedQuestions] = useState([]);
  const [loading, setLoading] = useState(false);

  const fetchData = useCallback(async () => {
    setLoading(true);
    try {
      const response = await fetch(`https://localhost:5201/PaperSetQuestion/PaperSetId?paperSetId=${id}`, {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${authState.token}`,
        },
      });

      if (response.ok) {
        const data = await response.json();
        setQuestions(data);
      } else {
        console.error('Failed to fetch data:', response.statusText);
      }
    } catch (error) {
      console.error('Error fetching data:', error);
    } finally {
      setLoading(false);
    }
  }, [authState.token, id]);

  const fetchUnrelatedQuestions = useCallback(async () => {
    setLoading(true);
    try {
      const response = await fetch(
        `https://localhost:5201/PaperSetQuestion/UnrelatedQuestionsByPaperSetId?paperSetId=${id}`,
        {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${authState.token}`,
          },
        }
      );

      if (response.ok) {
        const data = await response.json();
        setNotQuestions(data);
      } else {
        console.error('Failed to fetch data:', response.statusText);
      }
    } catch (error) {
      console.error('Error fetching data:', error);
    } finally {
      setLoading(false);
    }
  }, [id, authState.token]);

  useEffect(() => {
    fetchData();
    fetchUnrelatedQuestions();
  }, [fetchData, fetchUnrelatedQuestions]);

  const handleCheckboxChange = (questionId) => {
    setSelectedQuestions((prev) =>
      prev.includes(questionId)
        ? prev.filter((id) => id !== questionId)
        : [...prev, questionId]
    );
  };

  const handleAddQuestions = async () => {
    setLoading(true);
    try {
      const response = await fetch(
        `https://localhost:5201/PaperSet/AddQuestionToPaperSet?paperSetId=${id}`,
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${authState.token}`,
          },
          body: JSON.stringify(selectedQuestions.map(String)),
        }
      );

      if (response.ok) {
        fetchData();
        fetchUnrelatedQuestions();
        setSelectedQuestions([]);
      } else {
        console.error('Failed to add questions:', response.statusText);
      }
    } catch (error) {
      console.error('Error adding questions:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleRemoveQuestions = async () => {
    setLoading(true);
    try {
      const response = await fetch(
        `https://localhost:5201/PaperSetQuestion/RemoveQuestionsFromPaperSet?paperSetId=${id}`,
        {
          method: 'DELETE',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${authState.token}`,
          },
          body: JSON.stringify(selectedQuestions.map(String)),
        }
      );

      if (response.ok) {
        fetchData();
        fetchUnrelatedQuestions();
        setSelectedQuestions([]);
      } else {
        console.error('Failed to remove questions:', response.statusText);
      }
    } catch (error) {
      console.error('Error removing questions:', error);
    } finally {
      setLoading(false);
    }
  };

  // Function to group questions by category and normalize category names
  const groupQuestionsByCategory = (questions) => {
    return questions.reduce((groups, question) => {
      const normalizedCategory = question.category.toUpperCase(); // Normalize category to uppercase
      if (!groups[normalizedCategory]) {
        groups[normalizedCategory] = [];
      }
      groups[normalizedCategory].push(question);
      return groups;
    }, {});
  };

  // Group questions by category (both unrelated and related questions)
  const categorizedQuestions = groupQuestionsByCategory(notQuestions);
  const categorizedExistingQuestions = groupQuestionsByCategory(questions);

  return (
    <div className="p-8 bg-gray-50 min-h-screen">
      <h2 className="text-center text-3xl font-bold mb-8 text-gray-800">Paperset: {name}</h2>
      
      {loading && (
        <div className="absolute top-0 left-0 right-0 bottom-0 flex items-center justify-center bg-gray-200 bg-opacity-50 z-50">
          <div className="text-xl text-gray-700">Loading...</div>
        </div>
      )}
      
      <div className="flex space-x-8">
        {/* Unrelated Questions Section */}
        <div className="w-1/2">
          <h3 className="text-xl font-semibold text-gray-700 mb-4">Questions to Add</h3>
          <div className="bg-white rounded-lg shadow-lg p-6 space-y-6">
            {Object.keys(categorizedQuestions).map((category) => (
              <div key={category} className="border-b pb-4">
                <h4 className="text-lg font-semibold text-gray-800">{category}</h4>
                <table className="w-full text-sm text-left mt-4">
                  <thead className="bg-gray-200 text-gray-600">
                    <tr>
                      <th className="px-4 py-2">Select</th>
                      <th className="px-4 py-2">Question</th>
                    </tr>
                  </thead>
                  <tbody>
                    {categorizedQuestions[category].map((question) => (
                      <tr key={question.questionId} className="border-t">
                        <td className="px-4 py-2">
                          <input
                            type="checkbox"
                            checked={selectedQuestions.includes(question.id)}
                            onChange={() => handleCheckboxChange(question.id)}
                          />
                        </td>
                        <td className="px-4 py-2">{question.questionText}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            ))}
          </div>
          <button
            className="mt-4 px-6 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700"
            onClick={handleAddQuestions}
          >
            Add Selected Questions
          </button>
        </div>

        {/* Related Questions Section */}
        <div className="w-1/2">
          <h3 className="text-xl font-semibold text-gray-700 mb-4">Questions in Paperset</h3>
          <div className="bg-white rounded-lg shadow-lg p-6 space-y-6">
            {Object.keys(categorizedExistingQuestions).map((category) => (
              <div key={category} className="border-b pb-4">
                <h4 className="text-lg font-semibold text-gray-800">{category}</h4>
                <table className="w-full text-sm text-left mt-4">
                  <thead className="bg-gray-200 text-gray-600">
                    <tr>
                      <th className="px-4 py-2">Select</th>
                      <th className="px-4 py-2">Question</th>
                    </tr>
                  </thead>
                  <tbody>
                    {categorizedExistingQuestions[category].map((question) => (
                      <tr key={question.questionId} className="border-t">
                        <td className="px-4 py-2">
                          <input
                            type="checkbox"
                            checked={selectedQuestions.includes(question.id)}
                            onChange={() => handleCheckboxChange(question.id)}
                          />
                        </td>
                        <td className="px-4 py-2">{question.questionText}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            ))}
          </div>
          <div className="flex justify-between mt-6">
            <button
              className="px-6 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700"
              onClick={handleRemoveQuestions}
            >
              Remove Selected Questions
            </button>
            <button
              className="px-6 py-2 bg-gray-400 text-white rounded-lg hover:bg-gray-500"
              onClick={onClose}
            >
              Close
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PaperSetView;
