import React, { useState, useEffect } from "react";
import { useAuthContext } from "../../../Context/AuthContext";
import { useNavigate } from "react-router-dom";
import { showConfirmationDialog, showSuccessNotification, showErrorNotification, showTimeUpPopup } from "../../../Components/PopupConfirmations";

const QuestionDashboard = ({ paperSetId, paperSetName }) => {
    const { authState } = useAuthContext();
    const navigate = useNavigate(); // React Router hook for navigation
    const [questions, setQuestions] = useState([]);
    const [timer, setTimer] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [isTimeUp, setIsTimeUp] = useState(false);
    const [currentPage, setCurrentPage] = useState(0);
    const [currentPageStart, setCurrentPageStart] = useState(0);
    const [currentSection, setCurrentSection] = useState(1);
    const [selectedAnswers, setSelectedAnswers] = useState({});
    const [saving, setSaving] = useState(null); // Track saving state
    const [showSaved, setShowSaved] = useState(false);
    const [answeredQuestions, setAnsweredQuestions] = useState({}); // Track answered questions
    const pageRange = 10;
    const totalSections = 3;

    useEffect(() => {
        const fetchExamData = async () => {
            try {
                const response = await fetch(
                    `https://localhost:5201/CandidatePaperSet/GetQuestionsFromPaperSet/${authState.userId}/${paperSetId}`,
                    {
                        headers: {
                            Authorization: `Bearer ${authState.token}`,
                        },
                    }
                );

                if (!response.ok) {
                    throw new Error("Failed to fetch exam data.");
                }

                const data = await response.json();

                if (!data.paperSetQuestions || !data.timeInMinutes) {
                    throw new Error("API response is missing required fields.");
                }

                setQuestions(data.paperSetQuestions);
                setTimer(data.timeInMinutes * 60); // Set timer in seconds
            } catch (err) {
                setError(err.message);
            } finally {
                setLoading(false);
            }
        };

        fetchExamData();
    }, [authState.userId, paperSetId, authState.token]);

    useEffect(() => {
        if (timer > 0) {
            const timerInterval = setInterval(() => setTimer((prevTimer) => prevTimer - 1), 1000);
            return () => clearInterval(timerInterval);
        } else if (timer === 0) {
            setIsTimeUp(true);
            handleTimeUp(); // Automatically submit the exam when time is up
        }
    }, [timer]);

    const handleTimeUp = async () => {
        await showTimeUpPopup();  // Show time-up popup
        await submitExam();       // Automatically submit exam
    };
    const submitExam = async () => {
        try {
            const response = await fetch(`https://localhost:5201/CandidatePaperSet/FinishExam`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    Authorization: `Bearer ${authState.token}`,
                },
                body: JSON.stringify({
                    candidateId: authState.userId,
                    paperSetId: paperSetId,
                    paperSetName: paperSetName, 
                }),
            });

            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(errorData.message || "Failed to submit exam.");
            }

            const data = await response.json();

            // Show success notification
            await showSuccessNotification("Exam submitted successfully!");

            // Redirect to home page after successful submission
            navigate("/");
        } catch (error) {
            // Show error notification
            await showErrorNotification("Failed to submit the exam. Please try again.");
        }
    };


    const formatTime = (timeInSeconds) => {
        const minutes = Math.floor(timeInSeconds / 60);
        const seconds = timeInSeconds % 60;
        return `${minutes}:${seconds < 10 ? "0" : ""}${seconds}`;
    };

    const handlePageChange = (pageNumber) => {
        if (pageNumber >= 1 && pageNumber <= questions.length) {
            setCurrentPage(pageNumber - 1);
        }
    };

    const displayedPages = [];
    for (let i = currentPageStart; i < Math.min(currentPageStart + pageRange, questions.length); i++) {
        displayedPages.push(i + 1);
    }

    const handleNextPageRange = () => {
        if (currentPageStart + pageRange < questions.length) {
            setCurrentPageStart(currentPageStart + pageRange);
        }
    };

    const handlePreviousPageRange = () => {
        if (currentPageStart > 0) {
            setCurrentPageStart(currentPageStart - pageRange);
        }
    };

    const handleSubmitClick = async () => {
        const isConfirmed = await showConfirmationDialog("Are you sure you want to submit the exam?");
        if (isConfirmed) {
            await submitExam();
        }
    };

    const handleOptionChange = async (questionIndex, selectedOption) => {
        setSaving(questionIndex);
        setShowSaved(true);
        setSelectedAnswers((prevAnswers) => ({
            ...prevAnswers,
            [questionIndex]: selectedOption,
        }));

        setAnsweredQuestions((prev) => ({
            ...prev,
            [questionIndex]: true, // Mark question as answered
        }));

        const answerData = {
            candidateId: authState.userId,
            paperSetId: paperSetId,
            questionId: questions[questionIndex].questionId,
            answer: selectedOption,
        };

        try {
            const response = await fetch("https://localhost:5201/CandidatePaperSet/SaveAnswer", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    Authorization: `Bearer ${authState.token}`,
                },
                body: JSON.stringify(answerData),
            });

            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(errorData.message || "Failed to save answer.");
            }
        } catch (error) {
            console.error("Error saving answer:", error.message);
        } finally {
            setTimeout(() => {
                setSaving(null);
                setTimeout(() => setShowSaved(false), 500);
            }, 500);
        }
    };


    const handleResetAnswer = async (questionIndex) => {
        const resetData = {
            candidateId: authState.userId,
            paperSetId: paperSetId,
            questionId: questions[questionIndex].questionId,
        };

        try {
            const response = await fetch("https://localhost:5201/CandidatePaperSet/ResetAnswer", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    Authorization: `Bearer ${authState.token}`,
                },
                body: JSON.stringify(resetData),
            });

            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(errorData.message || "Failed to reset answer.");
            }

            setSaving(questionIndex);
            setShowSaved(true); // Show autosave indicator
            // Clear the selected answer for this question in the UI
            setSelectedAnswers((prevAnswers) => {
                const updatedAnswers = { ...prevAnswers };
                delete updatedAnswers[questionIndex];
                return updatedAnswers;
            });
            setAnsweredQuestions((prev) => {
                const updatedAnswered = { ...prev };
                delete updatedAnswered[questionIndex]; // Remove answered status
                return updatedAnswered;
            });

        } catch (error) {
            console.error("Error resetting answer:", error.message);
        } finally {
            setTimeout(() => {
                setSaving(null); // Switch to "Saved"
                setTimeout(() => setShowSaved(false), 500); // Hide after 2 sec
            }, 500);
        }
    };

    if (!authState.isAuthenticated) {
        return <div>Please log in to access the test.</div>;
    }

    if (loading) {
        return <div>Loading questions...</div>;
    }

    if (error) {
        return <div>Error: {error}</div>;
    }

    return (
        <div className="w-full h-screen flex flex-col p-8 bg-gray-100">


            {/* Top Navigation - Sections */}
            <div className="w-full mb-8">
                <div className="flex justify-between gap-9">
                    <div className="flex gap-2">

                        {/* Sections buttons */}
                        <button
                            className={`${currentSection === 1 ? "bg-blue-500 text-white" : "bg-gray-100 text-gray-700"
                                } border border-gray-300 rounded px-4 py-2 text-lg transition duration-300`}
                            onClick={() => setCurrentSection(1)}
                        >
                            Section 1
                        </button>
                        <button
                            className={`${currentSection === 2 ? "bg-blue-500 text-white" : "bg-gray-100 text-gray-700"
                                } border border-gray-300 rounded px-4 py-2 text-lg transition duration-300`}
                            onClick={() => setCurrentSection(2)}
                        >
                            Section 2
                        </button>
                        <button
                            className={`${currentSection === 3 ? "bg-blue-500 text-white" : "bg-gray-100 text-gray-700"
                                } border border-gray-300 rounded px-4 py-2 text-lg transition duration-300`}
                            onClick={() => setCurrentSection(3)}
                        >
                            Section 3
                        </button>
                        <button className="bg-blue-500 text-white rounded-full w-8 h-8 font-bold flex justify-center items-center mt-2">
                            i
                        </button>
                    </div>
                    <div className="flex gap-5 items-center">
                        {/* Autosave Indicator */}
                        {showSaved && (
                            <div
                                className={`p-2 rounded-md w-28 text-center text-sm transition-all duration-500 ease-in-out ${saving !== null ? "bg-blue-500 text-white animate-pulse" : "bg-green-500 text-white"}`}
                            >
                                {saving !== null ? "Saving..." : "Saved"}
                            </div>
                        )}

                        {/* Display Attempted Questions Count */}
                        <div className="text-lg font-bold text-gray-700 ">
                            Attempted: {Object.keys(answeredQuestions).length} / {questions.length}
                        </div>


                        {/* Timer Display */}
                        {timer !== null && (
                            <div className="text-center bg-blue-600 p-4 rounded-lg text-white">
                                <p className="text-xl font-bold">Time Left: {formatTime(timer)}</p>
                            </div>
                        )}
                    </div>
                </div>
            </div>
            <div className="text-center mb-6 z-10">
                <h1 className="text-3xl font-bold text-black">{paperSetName}</h1>
            </div>

            {/* Display Current Question */}
            {questions[currentPage] && (
                <div className="bg-white p-6 rounded-lg shadow-md border border-gray-300 mb-8">
                    <div className="flex gap-8">
                        {/* Left Column: Question */}
                        <div className="bg-gray-100 p-6 rounded-lg shadow-md w-1/2">
                            {/* Question Number */}
                            <p className="text-xl font-bold mb-4">Question {currentPage + 1}</p>
                            {/* Question Text */}
                            <p className="text-2xl font-bold">{questions[currentPage].question.questionText}</p>
                            {/* Image */}
                            {questions[currentPage].question.questionImage && (
                                <div className="mt-4">
                                    <img
                                        src={questions[currentPage].question.questionImage}
                                        alt={`Question ${currentPage + 1} image`}
                                        className="w-full h-auto"
                                    />
                                </div>
                            )}
                        </div>


                        {/* Right Column: Options */}
                        <div className="flex flex-col gap-4 w-1/2">
                            <p className="text-xl mb-4">Select an option from the below:</p>
                            {/* Options */}
                            {['option1', 'option2', 'option3', 'option4'].map((option, index) => (
                                <label
                                    key={index}
                                    className="flex items-center gap-2 p-2 bg-gray-100 border-gray-300 cursor-pointer hover:bg-gray-200 rounded-lg"
                                >
                                    <input
                                        type="radio"
                                        name={`question-${currentPage}`} // Use the question index for unique names
                                        value={questions[currentPage].question[option]}
                                        className="accent-blue-500"
                                        checked={selectedAnswers[currentPage] === questions[currentPage].question[option]} // Check if this option is selected
                                        onChange={() => handleOptionChange(currentPage, questions[currentPage].question[option])} // Update selected answer
                                        disabled={isTimeUp} // Disable input if time is up
                                    />
                                    <span>{questions[currentPage].question[option]}</span>

                                </label>
                            ))}
                            {/* Reset Button */}
                            <button
                                onClick={() => handleResetAnswer(currentPage)} // Reset the answer for the current question
                                disabled={isTimeUp} // Disable reset button when time is up
                                className="text-red-500 mt-4"
                            >
                                Reset Answer
                            </button>
                        </div>
                    </div>
                </div>
            )}

            {/* Pagination */}
            <div className="flex items-center justify-center gap-2">
                <button
                    onClick={handlePreviousPageRange}
                    disabled={currentPageStart === 0}
                    className="text-blue-500 text-2xl disabled:text-gray-400"
                >
                    &#9664;
                </button>
                {displayedPages.map((page) => (
                    <button
                        key={page}
                        onClick={() => handlePageChange(page)}
                        className={`border px-4 py-2 transition duration-300 ${currentPage === page - 1
                            ? "bg-blue-500 text-white"
                            : answeredQuestions[page - 1]
                                ? "bg-green-500 text-white"  // Change color for answered questions
                                : "bg-gray-100 text-gray-700"
                            }`}
                    >
                        {page}
                    </button>
                ))}

                <button
                    onClick={handleNextPageRange}
                    disabled={currentPageStart + pageRange >= questions.length}
                    className="text-blue-500 text-2xl disabled:text-gray-400"
                >
                    &#9654;
                </button>
            </div>
            <div className="flex justify-end mt-10">
                <button
                    onClick={handleSubmitClick}
                    className="bg-red-500 text-white px-4 py-2 rounded-lg"
                >
                    Submit Exam
                </button>
            </div>
        </div>
    );
};

export default QuestionDashboard;
