import React, { useState, useEffect } from 'react';
import { useAuthContext } from '../../../Context/AuthContext';
import EditCandidateProfile from '../../Candidate/Components/EditCandidateProfile'; // Import the EditCandidateProfile component
import { showStatusChangeConfirmation } from '../../../Components/PopupConfirmations';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUserCheck, faUserSlash, faPenToSquare } from '@fortawesome/free-solid-svg-icons';
import { CandidateProfile } from '../../Candidate';

const CandidateList = () => {
    const { authState } = useAuthContext();
    const [candidates, setCandidates] = useState([]);
    const [selectedCandidate, setSelectedCandidate] = useState(null); // Store selected candidate for editing
    const [isEditModalOpen, setIsEditModalOpen] = useState(false); // For edit candidate modal
    const [isModalOpen, setModalOpen] = useState(false);

    // Fetch candidates data
    useEffect(() => {
        const fetchData = async () => {
            try {
                if (!authState.token) {
                    console.error('Token is missing');
                    return;
                }

                const response = await fetch('https://localhost:5201/Candidate/GetAll', {
                    headers: {
                        'Authorization': `Bearer ${authState.token}`,
                    },
                });
                if (response.ok) {
                    const data = await response.json();
                    setCandidates(data);
                } else {
                    console.error('Failed to fetch data:', response.statusText);
                }
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        if (authState.isAuthenticated) {
            fetchData();
        }
    }, [authState]);

    // Handle status change for a candidate
    const handleStatusChange = async (candidateId, isActive) => {
        try {
            if (!authState.token) {
                console.error('Token is missing');
                return;
            }

            const response = await fetch(
                `https://localhost:5201/api/Account/ActivateDeactivate/?candidateId=${candidateId}&isActive=${isActive}`,
                {
                    method: 'POST',
                    headers: {
                        'Authorization': `Bearer ${authState.token}`,
                        'Content-Type': 'application/json',
                    },
                }
            );

            if (response.ok) {
                console.log('Candidate status updated successfully');
                setCandidates((prevCandidates) =>
                    prevCandidates.map((item) =>
                        item.id === candidateId ? { ...item, active: isActive } : item
                    )
                );
            } else {
                console.error('Failed to update status:', response.statusText);
            }
        } catch (error) {
            console.error('Error updating status:', error);
        }
    };

    // Open the edit modal
    const handleEditClick = (candidate) => {
        setSelectedCandidate(candidate);
        setIsEditModalOpen(true);
    };
    const handleFullNameClick = (candidate) => {
        setSelectedCandidate(candidate);
        setModalOpen(true);
    };

    // Close the edit modal
    const handleCloseModal = () => {
        setIsEditModalOpen(false);
        setModalOpen(false);
        setSelectedCandidate(null);
    };

    // Show confirmation popup before status change
    const confirmStatusChange = async (candidate) => {
        const isConfirmed = await showStatusChangeConfirmation(candidate);
        if (isConfirmed) {
            handleStatusChange(candidate.id, !candidate.active);
        }
    };

    return (
        <div className="p-5">
            <h2 className="text-3xl font-bold underline text-center mb-8">Candidates</h2>

            {/* Candidates Table */}
            <div className="overflow-x-auto shadow-md rounded-lg">
                <table className="w-full table-auto border-collapse mb-5 text-gray-800">
                    <thead className="bg-gray-200 text-lg text-gray-700">
                        <tr>
                            <th className="px-6 py-3 font-extrabold text-left">Sr no</th>
                            <th className="px-6 py-3 font-extrabold text-left">Full Name</th>
                            {/* <th className="px-6 py-3 font-extrabold text-left">Email</th> */}
                            <th className="px-6 py-3 font-extrabold text-left">Mobile</th>
                            <th className="px-6 py-3 font-extrabold text-left">Created On</th>
                            <th className="px-6 py-3 font-extrabold text-left">Active</th>
                            <th className="px-6 py-3 font-extrabold text-left">Actions</th>
                        </tr>
                    </thead>
                    <tbody className="bg-white">
                        {candidates.map((item, index) => (
                            <tr key={item.id} className="bg-white">
                                <td className="px-6 py-4">{index + 1}</td>
                                {/* <td className="px-6 py-4">{item.fullName}</td> */}
                                <td className="px-6 py-4 text-sm">
                                {/* Full Name */}
                                <span className="text-blue-600 hover:underline cursor-pointer" onClick={() => handleFullNameClick(item)}>
                                    {item.fullName}
                                </span>
                            </td>

                                {/* <td className="px-6 py-4">{item.emailAddress}</td>*/}
                                <td className="px-6 py-4">{item.mobileNumber}</td>
                                <td className="px-6 py-4">
                                    {new Date(item.createdOn).toLocaleDateString('en-GB', {
                                        day: '2-digit',
                                        month: '2-digit',
                                        year: 'numeric',
                                    })}
                                </td>
                                <td className="px-6 py-4">
                                    {item.active ? (
                                        <span className="text-green-500 font-bold">Yes</span>
                                    ) : (
                                        <span className="text-red-500 font-bold">No</span>
                                    )}
                                </td>
                                <td className="px-6 py-4 flex space-x-3">
                                    <button
                                        onClick={() => handleEditClick(item)}
                                        className="bg-yellow-500 hover:bg-yellow-700 text-white p-3 rounded-md flex items-center justify-center"
                                    >
                                        <FontAwesomeIcon icon={faPenToSquare} style={{ width: '18px', height: '20px' }} />
                                    </button>

                                    <button
                                        onClick={() => confirmStatusChange(item)}
                                        className={`${item.active
                                            ? 'bg-red-400 hover:bg-red-700'
                                            : 'bg-blue-500 hover:bg-blue-700'
                                            } text-white p-3 rounded-md flex items-center justify-center`}
                                    >
                                        {item.active ? (
                                            <FontAwesomeIcon icon={faUserSlash} style={{ width: '20px', height: '20px' }} />
                                        ) : (
                                            <FontAwesomeIcon icon={faUserCheck} style={{ width: '20px', height: '20px' }} />
                                        )}
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>

            {/* Edit Candidate Modal */}
            {isEditModalOpen && selectedCandidate && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center z-50">
                    <div className="bg-gray-200 rounded-lg shadow-xl w-full max-w-3xl h-auto max-h-[90vh] p-8 overflow-y-auto">
                        <EditCandidateProfile
                            id={selectedCandidate.id}
                            token={authState.token}
                            onClose={handleCloseModal}
                        />
                    </div>
                </div>
            )}
        </div>
    );
};

export default CandidateList;