import React, { useState } from 'react';

const ForgotPassword = () => {
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const [error, setError] = useState('');

  const handleForgotPassword = async () => {
    try {
      const response = await fetch('https://localhost:5201/api/Account/forgot-password', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email }),
      });

      if (response.ok) {
        setMessage('A password reset link has been sent to your email.');
        setError('');
      } else {
        const data = await response.json();
        setError(data.message || 'Failed to send password reset email.');
        setMessage('');
      }
    } catch (error) {
      console.error('Error during forgot password:', error);
      setError('An error occurred. Please try again later.');
      setMessage('');
    }
  };

  return (
      <div className="w-full max-w-lg p-1 space-y-8">
        <div className="text-center">
          <h2 className="text-4xl font-bold text-gray-900">Forgot Password</h2>
          <p className="text-sm text-gray-500 mt-2">
            Enter your email address to receive a password reset link.
          </p>
        </div>
        {message && (
          <div className="text-center text-sm text-green-500 bg-green-100 py-3 rounded">
            {message}
          </div>
        )}
        {error && (
          <div className="text-center text-sm text-red-500 bg-red-100 py-3 rounded">
            {error}
          </div>
        )}
        <form className="space-y-8">
          <div>
            <label className="block text-sm font-medium text-gray-700">Email address</label>
            <input
              id="email"
              name="email"
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
              className="mt-3 w-full px-4 py-3 border rounded-lg shadow-sm text-gray-900 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
            />
          </div>
          <div>
            <button
              type="button"
              onClick={handleForgotPassword}
              className="w-full py-3 px-6 text-base font-semibold text-white bg-indigo-600 hover:bg-indigo-700 rounded-lg shadow focus:ring-2 focus:ring-indigo-500 focus:outline-none"
            >
              Send Reset Link
            </button>
          </div>
        </form>
      </div>
  );
};

export default ForgotPassword;