import Swal from 'sweetalert2';

const defaultSweetAlertOptionsForPopup = {
  title: "Are you sure?",
  text: "You won't be able to revert this!",
  icon: "warning",
  showCancelButton: true,
  confirmButtonColor: "#3085d6",
  cancelButtonColor: "#d33",
  confirmButtonText: "Yes!",
};

const defaultSweetAlertOptionsForConfirmation = {
  title: "Success!",
  text: "Operation successful.",
  icon: "success",
};


// Helper to clone objects (structuredClone fallback)
const cloneObject = (obj) => JSON.parse(JSON.stringify(obj));

const showDialog = async (options, needConfirmation = false, confirmationOptions = null) => {
  const result = await Swal.fire(options);
  if (needConfirmation && confirmationOptions != null && result.isConfirmed) {
    await Swal.fire(confirmationOptions);
  }
  return result;
};


// Individual Dialogboxes used in the components...
export const showSignOutConfirmation = async (message = "You want to sign out?") => {
  const options = cloneObject(defaultSweetAlertOptionsForPopup);
  options.text = message;
  options.icon = "question";

  const result = await showDialog(options);
  return result.isConfirmed;
};


export const showDeleteConfirmation = async (message="Are you sure you want to delete this?") => {
  const options = cloneObject(defaultSweetAlertOptionsForPopup);
  options.title = message ;

  const result = await showDialog(options);
  return result.isConfirmed;
};


export const showSuccessNotification = async (message="Operation successful") => {
  const options = cloneObject(defaultSweetAlertOptionsForConfirmation);
  options.text = message;
  options.showConfirmButton = false;
  options.timer = 2000;

  await showDialog(options);
};

export const showErrorNotification = async (message="Operation Failed") => {
  const options = cloneObject(defaultSweetAlertOptionsForConfirmation);
  options.icon = "error";
  options.title = message;
  options.text = "An error occurred";
  options.showConfirmButton = true;

  await showDialog(options);
};


export const showStatusChangeConfirmation = async (candidate) => {
  const options = cloneObject(defaultSweetAlertOptionsForPopup);
  options.text = `You want to ${candidate.active ? "deactivate" : "activate"} this candidate?`;
  options.cancelButtonText = "No, keep it";

  const result = await showDialog(options);
  return result.isConfirmed;
};

export const showUpdateConfirmation = async (message = "You are about to update it!") => {
  const options = cloneObject(defaultSweetAlertOptionsForPopup); 
  options.text = message;

  const result = await showDialog(options); 
  return result.isConfirmed; 
};


export const showConfirmationDialog = async (message = "You are about to update it!") => {
  const options = cloneObject(defaultSweetAlertOptionsForPopup); 
  options.text = message;
  options.icon = "question";

  const result = await showDialog(options); 
  return result.isConfirmed; 
};

export const showTimeUpPopup = async () => {
  await Swal.fire({
    title: "Time is up!",
    text: "Your exam has been submitted.",
    icon: "warning",
    allowOutsideClick: false,
    allowEscapeKey: false,
    confirmButtonText: "OK",
  });
};