import React, { useState } from 'react';
import { useAuthContext } from '../../../Context/AuthContext';

const UpdateQuestion = ({ id, onClose }) => {
    const { authState } = useAuthContext();
    const [updatedCategory, setUpdatedCategory] = useState('');
    const [updatedQuestion, setUpdatedQuestion] = useState('');
    const [updatedOption1, setUpdatedOption1] = useState('');
    const [updatedOption2, setUpdatedOption2] = useState('');
    const [updatedOption3, setUpdatedOption3] = useState('');
    const [updatedOption4, setUpdatedOption4] = useState('');
    const [updatedAnswer, setUpdatedAnswer] = useState('');
    const [message, setMessage] = useState('');

    const handleUpdate = async () => {
        const updatedData = {
            Id: id,
            Category: updatedCategory,
            question: updatedQuestion,
            Option1: updatedOption1,
            Option2: updatedOption2,
            Option3: updatedOption3,
            Option4: updatedOption4,
            Answer: updatedAnswer,
        };

        try {
            const response = await fetch(`https://localhost:5201/Question/Update?Id=${id}`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${authState.token}`,
                },
                body: JSON.stringify(updatedData),
            });

            const data = await response.json();
            if (response.ok) {
                console.log('Updated Successfully');
                setMessage('Updated Successfully!');
                window.alert('Updated Successfully!');
            } else {
                console.error('Failed to update data:', response.statusText);
                setMessage('Failed to update question.');
            }
        } catch (error) {
            console.error('Error updating question:', error);
            setMessage('Error updating question.');
        }
    };

    return (
        <div className="mx-auto w-full max-w-2xl p-6 rounded-lg shadow-md bg-gray-200">
            <h2 className="mb-6 text-2xl font-bold leading-tight text-black">
                Update Question
            </h2>

            <div className="mb-4">
                <label className="block mb-2 text-sm font-medium text-black">
                    Question ID
                </label>
                <input
                    value={id}
                    type="text"
                    disabled
                    className="w-full bg-gray-100 border border-gray-300 rounded-lg p-2.5 text-sm text-gray-700 cursor-not-allowed"
                />
            </div>

            {[  
                { label: 'Updated Category', value: updatedCategory, setter: setUpdatedCategory },
                { label: 'Updated Question', value: updatedQuestion, setter: setUpdatedQuestion },
                { label: 'Updated Option 1', value: updatedOption1, setter: setUpdatedOption1 },
                { label: 'Updated Option 2', value: updatedOption2, setter: setUpdatedOption2 },
                { label: 'Updated Option 3', value: updatedOption3, setter: setUpdatedOption3 },
                { label: 'Updated Option 4', value: updatedOption4, setter: setUpdatedOption4 },
                { label: 'Updated Answer', value: updatedAnswer, setter: setUpdatedAnswer },
            ].map(({ label, value, setter }, index) => (
                <div className="mb-4" key={index}>
                    <label className="block mb-2 text-sm font-medium text-black">
                        {label}
                    </label>
                    <input
                        type="text"
                        value={value}
                        onChange={(e) => setter(e.target.value)}
                        className="w-full bg-white border border-gray-300 rounded-lg p-2.5 text-sm text-gray-900 focus:ring-blue-500 focus:border-blue-500"
                        placeholder={`Enter ${label}`}
                    />
                </div>
            ))}

            <div className="flex items-center justify-between">
                <button
                    onClick={() => {
                        handleUpdate();
                        onClose();
                    }}
                    className="px-5 py-2.5 bg-blue-600 text-white text-sm font-medium rounded-lg hover:bg-blue-700 focus:ring-4 focus:outline-none focus:ring-blue-300"
                >
                    Update Question
                </button>
                <button
                    onClick={onClose}
                    className="px-5 py-2.5 bg-red-600 text-white text-sm font-medium rounded-lg hover:bg-red-700 focus:ring-4 focus:outline-none focus:ring-red-300"
                >
                    Close
                </button>
            </div>

            {message && (
                <p className="mt-4 text-sm font-medium text-green-600">
                    {message}
                </p>
            )}
        </div>
    );
};

export default UpdateQuestion;
