import { Link, useNavigate } from "react-router-dom";
import React, { useState } from "react";
import logo from "../assets/images/UpVision.FullLogo.png";
import { showSignOutConfirmation } from "../Components/PopupConfirmations";
import { useAuthContext } from "../Context/AuthContext";

const Navbar = () => {
    const { authState, logoutWithAuthContext } = useAuthContext();
    const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
    const [profileMenuOpen, setProfileMenuOpen] = useState(false);
    const navigate = useNavigate();

    const handleSignOut = async () => {
        const isConfirmed = await showSignOutConfirmation();
        if (isConfirmed) {
            logoutWithAuthContext();
            navigate("/login");
        }
    };

    const toggleMobileMenu = () => {
        setMobileMenuOpen((prev) => !prev);
    };

    const toggleProfileMenu = () => {
        setProfileMenuOpen((prev) => !prev);
    };

    const renderLinks = (role) => {
        const links = {
            admin: [
                { to: "/", label: "Home" },
                { to: "/About", label: "About" },
                { to: "/CandidateList", label: "Candidates" },
                { to: "/PaperSetList", label: "PaperSets" },
                { to: "/Question", label: "Questions" },
                { to: "/Status", label: "Status" },
            ],
            candidate: [
                { to: "/", label: "Home" },
                { to: "/About", label: "About" },
                { to: "/CandidateProfile", label: "Profile" },
            ],
        };

        return (links[role] || []).map((link) => (
            <Link
                key={link.to}
                className="text-white hover:bg-gray-700 hover:text-gray-300 rounded-md px-3 py-2 text-sm font-medium transition duration-150"
                to={link.to}
            >
                {link.label}
            </Link>
        ));
    };

    if (!authState.isAuthenticated) return null;

    return (
        <div className="min-h-full sticky top-0">
            {/* Navbar */}
            <nav className="bg-black shadow">
                <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
                    <div className="flex items-center justify-between h-16">
                        {/* Logo and Links */}
                        <div className="flex items-center space-x-4">
                            <img className="h-8 w-auto" src={logo} alt="UpVision Logo" />
                            <div className="hidden md:flex space-x-4">
                                {renderLinks(authState.role.toLowerCase())}
                            </div>
                        </div>

                        {/* Profile and Sign Out */}
                        <div className="hidden md:flex items-center space-x-4">
                            {/* Notifications */}
                            <button
                                className="text-gray-400 hover:text-white relative"
                                onClick={() => alert("No new notifications")}
                            >
                                <svg
                                    className="w-6 h-6"
                                    fill="none"
                                    viewBox="0 0 24 24"
                                    strokeWidth="1.5"
                                    stroke="currentColor"
                                    aria-hidden="true"
                                >
                                    <path
                                        strokeLinecap="round"
                                        strokeLinejoin="round"
                                        d="M14.857 17.082a23.848 23.848 0 0 0 5.454-1.31A8.967 8.967 0 0 1 18 9.75V9A6 6 0 0 0 6 9v.75a8.967 8.967 0 0 1-2.312 6.022c1.733.64 3.56 1.085 5.455 1.31m5.714 0a24.255 24.255 0 0 1-5.714 0m5.714 0a3 3 0 1 1-5.714 0"
                                    />
                                </svg>
                            </button>

                            {/* Profile Dropdown */}
                            <div className="relative">
                                <button
                                    className="flex items-center text-gray-400 hover:text-white"
                                    onClick={toggleProfileMenu}
                                >
                                    <span className="sr-only">Open user menu</span>
                                    <div className="relative w-8 h-8 overflow-hidden bg-gray-100 rounded-full dark:bg-gray-600">
                                        {authState.profileImage ? (
                                            <img
                                                src={authState.profileImage}
                                                alt="Profile"
                                                className="w-full h-full object-cover"
                                            />
                                        ) : (
                                            <svg
                                                className="absolute w-10 h-10 text-gray-400 -left-1"
                                                fill="currentColor"
                                                viewBox="0 0 20 20"
                                                xmlns="http://www.w3.org/2000/svg"
                                            >
                                                <path
                                                    fillRule="evenodd"
                                                    d="M10 9a3 3 0 100-6 3 3 0 000 6zm-7 9a7 7 0 1114 0H3z"
                                                    clipRule="evenodd"
                                                ></path>
                                            </svg>
                                        )}
                                    </div>
                                </button>
                                {profileMenuOpen && (
                                    <div className="absolute right-0 mt-2 w-auto bg-white shadow-lg rounded-md py-3 z-10">
                                        <div className="flex items-center px-4 py-3 text-gray-800">
                                            {/* Profile Photo and Details */}
                                            <div className="flex items-center space-x-3">
                                                <div className="text-sm">
                                                    <div className="text-gray-900 font-medium text-lg">
                                                        {authState.email}
                                                    </div>
                                                    <div className="text-gray-600">
                                                        {authState.role}
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                )}
                            </div>

                            {/* Sign Out Button */}
                            <button
                                className="text-gray-400 hover:text-white px-3 py-2 text-sm font-medium transition duration-150"
                                onClick={handleSignOut}
                            >
                                Sign Out
                            </button>
                        </div>

                        {/* Mobile Menu Toggle */}
                        <div className="md:hidden -mr-2">
                            <button
                                type="button"
                                className="p-2 text-gray-400 hover:text-white focus:outline-none"
                                onClick={toggleMobileMenu}
                            >
                                <svg
                                    className="w-6 h-6"
                                    fill="none"
                                    viewBox="0 0 24 24"
                                    strokeWidth="1.5"
                                    stroke="currentColor"
                                    aria-hidden="true"
                                >
                                    <path
                                        strokeLinecap="round"
                                        strokeLinejoin="round"
                                        d="M4 6h16M4 12h16M4 18h16"
                                    />
                                </svg>
                            </button>
                        </div>
                    </div>
                </div>
                {/* Mobile Menu */}
                {mobileMenuOpen && (
                    <div className="md:hidden bg-gray-900">
                        <div className="flex flex-col items-center pb-3 pt-2 space-y-4">
                            {/* Profile Content and Notifications */}
                            <div className="flex items-center justify-between w-full px-4 py-2 z-">
                                {/* Profile Photo and Details */}
                                <div className="flex items-center space-x-2">
                                    <div className="relative w-10 h-10 overflow-hidden bg-gray-100 rounded-full dark:bg-gray-600">
                                        <svg
                                            className="absolute w-12 h-12 text-gray-400 -left-1"
                                            fill="currentColor"
                                            viewBox="0 0 20 20"
                                            xmlns="http://www.w3.org/2000/svg"
                                        >
                                            <path
                                                fillRule="evenodd"
                                                d="M10 9a3 3 0 100-6 3 3 0 000 6zm-7 9a7 7 0 1114 0H3z"
                                                clipRule="evenodd"
                                            ></path>
                                        </svg>
                                    </div>
                                    <div className="text-white text-sm mt-2 ml-2">
                                        <div>
                                            <span className="font-semibold text-lg">{authState.email}</span>
                                        </div>
                                        <div className="text-gray-600">
                                            <span className="block">{authState.role}</span>
                                        </div>
                                    </div>
                                </div>

                                {/* Notification Icon */}
                                <button
                                    className="text-gray-400 hover:text-white"
                                    onClick={() => alert("No new notifications")}
                                >
                                    <svg
                                        className="w-6 h-6"
                                        fill="none"
                                        viewBox="0 0 24 24"
                                        strokeWidth="1.5"
                                        stroke="currentColor"
                                        aria-hidden="true"
                                    >
                                        <path
                                            strokeLinecap="round"
                                            strokeLinejoin="round"
                                            d="M14.857 17.082a23.848 23.848 0 0 0 5.454-1.31A8.967 8.967 0 0 1 18 9.75V9A6 6 0 0 0 6 9v.75a8.967 8.967 0 0 1-2.312 6.022c1.733.64 3.56 1.085 5.455 1.31m5.714 0a24.255 24.255 0 0 1-5.714 0m5.714 0a3 3 0 1 1-5.714 0"
                                        />
                                    </svg>
                                </button>
                            </div>

                            {/* Navigation Links */}
                            <div className="flex flex-col space-y-2 w-full text-center">
                                {renderLinks(authState.role.toLowerCase())}
                            </div>

                            <div className="border-t border-white my-4 w-full"></div>
                            <button className="text-white px-4 py-2" onClick={handleSignOut}>
                                Sign Out
                            </button>
                        </div>
                    </div>
                )}

            </nav>
        </div>
    );
};

export default Navbar;
