import React, { useState } from 'react';
import Register from './Register';
import ForgotPassword from './ForgotPassword';
import { useAuthContext } from '../../Context/AuthContext';
import { useNavigate } from 'react-router-dom';

const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const { loginWithAuthContext } = useAuthContext();
  const navigateTo = useNavigate();

  const [isPasswordVisible, setPasswordVisible] = useState(false);
  // const [isLoggedIn, setLoggedIn] = useState(false);
  // const [userLoggedIn, setUserLoggedIn] = useState(false);
  const [register, setRegister] = useState(false);
  const [showForgotPassword, setShowForgotPassword] = useState(false);

  const handleLogin = async (e) => {
    e.preventDefault(); // Prevent default form submission
    try {
      const response = await fetch('https://localhost:5201/api/Account/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email, password }),
      });

      if (response.ok) {
        const data = await response.json();
        loginWithAuthContext(data.token, data.userID, data.email, data.role);
        setError('');
        navigateTo('/home');
      }
      else {
        const data = await response.json();
        setError(data.message || 'Login failed');
      }
    }
    catch (error) {
      console.error('Error during login:', error);
      setError('Login failed. Please try again.');
    }
  };

  const togglePasswordVisibility = () => {
    setPasswordVisible(!isPasswordVisible);
  };

  const handleForgotPassword = () => {
    setShowForgotPassword(true);
  };

  const handleCloseForgotPassword = () => {
    setShowForgotPassword(false);
  };

  const handleRegister = () => {
    setRegister(true);
  };

  // if (isLoggedIn) {
  //   return <App data={email} />;
  // }

  // if (userLoggedIn) {
  //   return <App data={email} />;
  // }

  if (register) {
    return <Register />;
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-100">
      <div className="w-full max-w-lg p-12 space-y-8 bg-white rounded-lg shadow-lg">
        {showForgotPassword ? (
          <ForgotPassword onClose={handleCloseForgotPassword} />
        ) : (
          <div>
            <div className="text-center">
              <h2 className="text-4xl font-bold text-gray-900">Sign in to your account</h2>
            </div>
            {error && (
              <div className="text-center text-sm text-red-500 bg-red-100 py-3 rounded">
                {error}
              </div>
            )}
            <form className="space-y-8" onSubmit={handleLogin}>
              <div>
                <label className="block text-sm font-medium text-gray-700 pt-10">Email address</label>
                <input
                  id="email"
                  name="email"
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                  className="mt-3 w-full px-4 py-3 border rounded-lg shadow-sm text-gray-900 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700">Password</label>
                <div className="relative mt-3">
                  <input
                    id="password"
                    name="password"
                    type={isPasswordVisible ? 'text' : 'password'}
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                    className="w-full px-4 py-3 border rounded-lg shadow-sm text-gray-900 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                  />
                  <button
                    type="button"
                    onClick={togglePasswordVisibility}
                    className="absolute inset-y-0 right-3 flex items-center text-sm text-indigo-600 hover:text-indigo-800 focus:outline-none"
                  >
                    {isPasswordVisible ? 'Hide' : 'Show'}
                  </button>
                </div>
              </div>
              <button
                type="button"
                onClick={handleForgotPassword}
                className="block text-sm text-indigo-600 hover:text-indigo-800 focus:outline-none mt-2"
              >
                Forgot password?
              </button>
              <div className="flex flex-col space-y-4 mt-6">
                <button
                  type="submit"
                  className="w-full py-3 px-6 text-base font-semibold text-white bg-indigo-600 hover:bg-indigo-700 rounded-lg shadow focus:ring-2 focus:ring-indigo-500 focus:outline-none"
                >
                  Sign In
                </button>
                <button
                  type="button"
                  onClick={handleRegister}
                  className="w-full py-3 px-6 text-base font-semibold text-indigo-600 border border-indigo-600 hover:bg-indigo-50 rounded-lg shadow-md focus:ring-2 focus:ring-indigo-500 focus:outline-none"
                >
                  Register Now
                </button>
              </div>
            </form>
          </div>
        )}
      </div>
    </div>
  );
};

export default Login;