import React, { useState, useEffect } from 'react';
import { useAuthContext } from '../../../Context/AuthContext';
import { useNavigate } from 'react-router-dom';
import { showSuccessNotification, showUpdateConfirmation } from '../../../Components/PopupConfirmations';
const EditCandidateProfile = ({ onClose }) => {
    const navigate = useNavigate();
    const { authState } = useAuthContext();
    const [updatedFirstName, setUpdatedFirstName] = useState('');
    const [updatedMiddleName, setUpdatedMiddleName] = useState('');
    const [updatedLastName, setUpdatedLastName] = useState('');
    const [updatedEmailAddress, setUpdatedEmailAddress] = useState('');
    const [updatedMobileNumber, setUpdatedMobileNumber] = useState('');
    const [updatedPAN, setUpdatedPAN] = useState('');
    const updatedActive = true;
    const [paddressLine, setpaddressLine] = useState('');
    const [pcity, setpcity] = useState('');
    const [pstate, setpstate] = useState('');
    const [pzipCode, setpzipCode] = useState('');
    const [pcountry, setpcountry] = useState('');
    const [caddressLine, setcaddressLine] = useState('');
    const [ccity, setccity] = useState('');
    const [cstate, setcstate] = useState('');
    const [czipCode, setczipCode] = useState('');
    const [ccountry, setccountry] = useState('');
    const [isChecked, setIsChecked] = useState(false);
    const [isUpdated, setIsUpdated] = useState(false);
    const [resumeFile, setResumeFile] = useState(null);


    // Pre-fetching candidate data
    useEffect(() => {
        const fetchCandidateData = async () => {
            try {
                const response = await fetch(`https://localhost:5201/Candidate/Id?id=${authState.userId}`, {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${authState.token}`,
                    },
                });
                const data = await response.json();
                if (data) {
                    setUpdatedFirstName(data.firstName);
                    setUpdatedMiddleName(data.middleName);
                    setUpdatedLastName(data.lastName);
                    setUpdatedEmailAddress(data.emailAddress);
                    setUpdatedMobileNumber(data.mobileNumber);
                    setUpdatedPAN(data.pan);
                    setpaddressLine(data.permanentAddress.addressLine);
                    setpcity(data.permanentAddress.city);
                    setpstate(data.permanentAddress.state);
                    setpzipCode(data.permanentAddress.zipCode);
                    setpcountry(data.permanentAddress.country);
                    setcaddressLine(data.currentAddress.addressLine);
                    setccity(data.currentAddress.city);
                    setcstate(data.currentAddress.state);
                    setczipCode(data.currentAddress.zipCode);
                    setccountry(data.currentAddress.country);
                }
            } catch (error) {
                console.error("Error fetching candidate data:", error);
            }
        };

        fetchCandidateData();
    }, [authState.userId, authState.token]);

    const handleCheckboxChange = () => {
        setIsChecked(!isChecked);
    };

    const handleFileChange = (event) => {
        setResumeFile(event.target.files[0]);
    };
    const handleUpdate = async () => {
        let updatedData = {
            firstName: updatedFirstName,
            middleName: updatedMiddleName,
            lastName: updatedLastName,
            emailAddress: updatedEmailAddress,
            mobileNumber: updatedMobileNumber,
            pan: updatedPAN,
            active: updatedActive,
            createdBy_UserId: "",
            updatedBy_UserId: authState.userId,
            permanentAddress: {
                firstName: updatedFirstName,
                lastName: updatedLastName,
                addressLine: paddressLine,
                city: pcity,
                state: pstate,
                zipCode: pzipCode,
                country: pcountry
            },
            currentAddress: {
                firstName: updatedFirstName,
                lastName: updatedLastName,
                addressLine: caddressLine,
                city: ccity,
                state: cstate,
                zipCode: czipCode,
                country: ccountry
            }
        };

        if (isChecked) {
            updatedData = {
                firstName: updatedFirstName,
                middleName: updatedMiddleName,
                lastName: updatedLastName,
                emailAddress: updatedEmailAddress,
                mobileNumber: updatedMobileNumber,
                pan: updatedPAN,
                active: updatedActive,
                createdBy_UserId: "",
                updatedBy_UserId: authState.userId,  // Using the userId from authState
                permanentAddress: {
                    firstName: updatedFirstName,
                    lastName: updatedLastName,
                    addressLine: paddressLine,
                    city: pcity,
                    state: pstate,
                    zipCode: pzipCode,
                    country: pcountry
                },
                currentAddress: {
                    firstName: updatedFirstName,
                    lastName: updatedLastName,
                    addressLine: paddressLine,
                    city: pcity,
                    state: pstate,
                    zipCode: pzipCode,
                    country: pcountry
                }
            };
        }

        const result = await showUpdateConfirmation("You are about to update your profile details.");

        if (result) {
            console.log(updatedData);

            const response = await fetch(`https://localhost:5201/Candidate/Update?id=${authState.userId}`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${authState.token}`,
                },
                body: JSON.stringify(updatedData),
            });

            if (response.ok) {
                setIsUpdated(true);
                setTimeout(() => {
                    setIsUpdated(false);
                }, 2000);

                await showSuccessNotification("Profile updated successfully");
                setTimeout(() => {
                    onClose();
                }, 2200);
            } else {
                console.error('Failed to update', response.statusText);
            }
        }
    };



    return (
        <div className='min-h-screen'>
            {isUpdated && (
                <div className="bg-teal-200 border-t-4 border-teal-600 rounded-b text-teal-900 px-4 py-3 shadow-lg mb-5" role="alert">
                    <div className="flex">
                        <div className="py-1"><svg className="fill-current h-6 w-6 text-teal-600 mr-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M2.93 17.07A10 10 0 1 1 17.07 2.93 10 10 0 0 1 2.93 17.07zm12.73-1.41A8 8 0 1 0 4.34 4.34a8 8 0 0 0 11.32 11.32zM9 11V9h2v6H9v-4zm0-6h2v2H9V5z" /></svg></div>
                        <div>
                            <p className="font-semibold">Details updated successfully!</p>
                            <p className="text-sm">Your profile has been successfully updated.</p>
                        </div>
                    </div>
                </div>)}
            <div className=''>
                <h2 className='my-4 text-3xl font-semibold text-gray-800 tracking-tight'>Update Candidate Profile</h2>

                <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mb-6">
                    <div>
                        <label className="block mb-2 text-sm font-medium text-gray-700">First Name</label>
                        <input type="text" value={updatedFirstName} onChange={(e) => setUpdatedFirstName(e.target.value)} className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" placeholder="Enter First Name" />
                    </div>
                    <div>
                        <label className="block mb-2 text-sm font-medium text-gray-700">Middlename</label>
                        <input type="text" value={updatedMiddleName} onChange={(e) => setUpdatedMiddleName(e.target.value)} className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" placeholder="Enter Middlename" />
                    </div>
                </div>

                <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mb-6">
                    <div>
                        <label className="block mb-2 text-sm font-medium text-gray-700">Last Name</label>
                        <input type="text" value={updatedLastName} onChange={(e) => setUpdatedLastName(e.target.value)} className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" placeholder="Enter Last Name" />
                    </div>
                    <div>
                        <label className="block mb-2 text-sm font-medium text-gray-700">Email Address</label>
                        <input type="email" value={updatedEmailAddress} className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" placeholder="Enter Email Address" />
                    </div>
                </div>

                <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mb-6">
                    <div>
                        <label className="block mb-2 text-sm font-medium text-gray-700">Mobile Number</label>
                        <input type="text" value={updatedMobileNumber} onChange={(e) => setUpdatedMobileNumber(e.target.value)} className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" placeholder="Enter Mobile Number" />
                    </div>
                    <div>
                        <label className="block mb-2 text-sm font-medium text-gray-700">PAN</label>
                        <input type="text" value={updatedPAN} onChange={(e) => setUpdatedPAN(e.target.value)} className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" placeholder="Enter PAN" />
                    </div>
                </div>

                {/* Address Inputs */}
                <div className="mb-6">
                    <label className="block mb-2 text-sm font-medium text-gray-700">Permanent Address</label>
                    <input type="text" value={paddressLine} onChange={(e) => setpaddressLine(e.target.value)} className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" placeholder="Enter Permanent Address" />
                </div>

                <div className="grid grid-cols-1 sm:grid-cols-4 gap-4 mb-6">
                    <div>
                        <label className="block mb-2 text-sm font-medium text-gray-700">City</label>
                        <input value={pcity} onChange={(e) => setpcity(e.target.value)} type="text" placeholder='City' className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" />
                    </div>
                    <div>
                        <label className="block mb-2 text-sm font-medium text-gray-700">State</label>
                        <input value={pstate} onChange={(e) => setpstate(e.target.value)} type="text" placeholder='State' className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" />
                    </div>
                    <div>
                        <label className="block mb-2 text-sm font-medium text-gray-700">Zipcode</label>
                        <input value={pzipCode} onChange={(e) => setpzipCode(e.target.value)} type="text" placeholder='Zipcode' className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" />
                    </div>
                    <div>
                        <label className="block mb-2 text-sm font-medium text-gray-700">Country</label>
                        <input value={pcountry} onChange={(e) => setpcountry(e.target.value)} type="text" placeholder='Country' className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" />
                    </div>
                </div>

                <div className="mb-6">
                    <input type="checkbox" checked={isChecked} onChange={handleCheckboxChange} />
                    <label className="text-sm font-medium text-gray-700">Same as Permanent Address</label>
                </div>

                {/* Current Address Inputs (if different) */}
                {isChecked ? null : (
                    <div>
                        <div className="mb-6">
                            <label className="block mb-2 text-sm font-medium text-gray-700">Current Address</label>
                            <input value={caddressLine} onChange={(e) => setcaddressLine(e.target.value)} type="text" placeholder="Enter Current Address" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" />
                        </div>

                        <div className="grid grid-cols-1 sm:grid-cols-4 gap-4 mb-6">
                            <div>
                                <label className="block mb-2 text-sm font-medium text-gray-700">City</label>
                                <input value={ccity} onChange={(e) => setccity(e.target.value)} type="text" placeholder='City' className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" />
                            </div>
                            <div>
                                <label className="block mb-2 text-sm font-medium text-gray-700">State</label>
                                <input value={cstate} onChange={(e) => setcstate(e.target.value)} type="text" placeholder='State' className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" />
                            </div>
                            <div>
                                <label className="block mb-2 text-sm font-medium text-gray-700">Zipcode</label>
                                <input value={czipCode} onChange={(e) => setczipCode(e.target.value)} type="text" placeholder='Zipcode' className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" />
                            </div>
                            <div>
                                <label className="block mb-2 text-sm font-medium text-gray-700">Country</label>
                                <input value={ccountry} onChange={(e) => setccountry(e.target.value)} type="text" placeholder='Country' className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5" />
                            </div>
                        </div>
                    </div>
                )}

                {/* Resume Upload */}
                {/* <div className="mb-6">
                    <input type="file" onChange={handleFileChange} />
                    
                </div> */}

                <div className="flex justify-center space-x-4 mt-6">
                    <button onClick={handleUpdate} className="bg-blue-500 text-white px-4 py-2 rounded">Update</button>
                    <button onClick={onClose} className="bg-gray-500 text-white px-4 py-2 rounded">Close</button>
                </div>

            </div>
        </div>
    );
};

export default EditCandidateProfile;
