import React, { useState, useEffect } from 'react';
import ShowByStaus from './ShowStatusByResult';
import ShowStatusByEmail from './ShowStatusByEmail';
import { useAuthContext } from '../../../Context/AuthContext';

const Status = (props) => {
    const { authState } = useAuthContext();
    const [candidate, setCandidate] = useState([]);
    const [status, setStatus] = useState([]);
    const [id, setId] = useState('');
    const [isPopupOpen, setPopupOpen] = useState(false);
    const [isPopupOpenForId, setPopupOpenForId] = useState(false);
    const [isCurrentContentVisible, setCurrentContentVisibility] = useState(true);
    let count = 0;

    const handleOpenPopup = (status) => {
        setPopupOpen(true);
        setStatus(status);
        setCurrentContentVisibility(false);
    };

    const handleClosePopup = () => {
        setPopupOpen(false);
        setStatus(null);
        setCurrentContentVisibility(true);
    };

    const handleOpenPopupForId = (id) => {
        setPopupOpenForId(true);
        setId(id);
        setCurrentContentVisibility(false);
    };

    const handleClosePopupForId = () => {
        setPopupOpenForId(false);
        setId(null);
        setCurrentContentVisibility(true);
    };

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('https://localhost:5201/CandidatePaperSet/GetAll', {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Bearer ${authState.token}`,
                    },
                });

                if (response.ok) {
                    const data = await response.json();
                    setCandidate(data);
                } else {
                    console.error('Failed to fetch data:', response.statusText);
                }
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        fetchData();
    }, [props.token]);

    return (
        <div className="p-6 min-h-screen">
            {isCurrentContentVisible && (
                <div>
                    <h2 className="text-center text-3xl font-bold leading-9 mb-6 underline text-gray-700">Candidate Status</h2>

                    <div className="flex flex-col md:flex-row justify-end mb-4 space-x-3">
                        <div className="flex items-center space-x-1">
                            <select
                                className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg w-44 px-2 py-2"
                                id="result"
                                onChange={(e) => setStatus(e.target.value)}
                                name="result"
                            >
                                <option className="bg-gray-500 text-white">Search By Result</option>
                                <option className="bg-gray-500 text-white" value="true">Attempted</option>
                                <option className="bg-gray-500 text-white" value="false">Not-Attempted</option>
                            </select>
                            <button
                                className="px-3 py-2 bg-blue-500 hover:bg-blue-700 text-white font-semibold rounded shadow-md"
                                onClick={() => handleOpenPopup(status)}
                            >
                                Search
                            </button>
                        </div>

                        <div className="flex items-center space-x-1 mt-4 md:mt-0">
                            <input
                                type="text"
                                value={id}
                                onChange={(e) => setId(e.target.value)}
                                className="w-44 bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg px-2 py-2"
                                placeholder="Search By Email"
                            />
                            <button
                                className="px-3 py-2 bg-blue-500 hover:bg-blue-700 text-white font-semibold rounded shadow-md"
                                onClick={() => handleOpenPopupForId(id)}
                            >
                                Search
                            </button>
                        </div>
                    </div>

                    <div className="overflow-x-auto rounded-lg shadow-md">
                        <table className="w-full text-sm text-center text-gray-700 bg-white">
                            <thead className="text-lg font-semibold bg-gray-200 text-black">
                                <tr>
                                    <th className="px-6 py-4">Sr no</th>
                                    <th className="px-6 py-4">Candidate Name</th>
                                    <th className="px-6 py-4">PaperSet</th>
                                    <th className="px-6 py-4">Status</th>
                                    <th className="px-6 py-4">Result</th>
                                    <th className="px-6 py-4">Start Time</th>
                                    <th className="px-6 py-4">Submitted On</th>
                                </tr>
                            </thead>
                            <tbody>
                                {candidate.map((item) => (
                                    <tr className="border-b hover:bg-gray-100 transition-all duration-200" key={item.id}>
                                        <td className="px-6 py-4">{++count}</td>
                                        <td className="px-6 py-4">{item.fullName}</td>
                                        <td className="px-6 py-4">{item.paperSetName}</td>
                                        <td className="px-6 py-4">{item.status ? 'Attempted' : 'Not-Attempted'}</td>
                                        <td className="px-6 py-4">{item.score}%</td>
                                        <td className="px-6 py-4">
                                            {item.startTime ? (
                                                <>
                                                    {new Date(item.startTime).toLocaleDateString('en-GB', {
                                                        day: '2-digit',
                                                        month: '2-digit',
                                                        year: 'numeric',
                                                    })}{' '}
                                                    {new Date(item.startTime).toLocaleTimeString('en-GB', {
                                                        hour: '2-digit',
                                                        minute: '2-digit',
                                                        second: '2-digit',
                                                    })}
                                                </>
                                            ) : (
                                                '-'
                                            )}
                                        </td>

                                        <td className="px-6 py-4">
                                            {item.submittedOn ? (
                                                <>
                                                    {new Date(item.submittedOn).toLocaleDateString('en-GB', {
                                                        day: '2-digit',
                                                        month: '2-digit',
                                                        year: 'numeric',
                                                    })}{' '}
                                                    {new Date(item.submittedOn).toLocaleTimeString('en-GB', {
                                                        hour: '2-digit',
                                                        minute: '2-digit',
                                                        second: '2-digit',
                                                    })}
                                                </>
                                            ) : (
                                                '-'
                                            )}
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>
            )}

            {isPopupOpen && <ShowByStaus status={status} token={props.token} onClose={handleClosePopup} />}
            {isPopupOpenForId && <ShowStatusByEmail id={id} token={props.token} onClose={handleClosePopupForId} />}
        </div>
    );
};

export default Status;
