import React, { useState } from 'react';
import { showSuccessNotification } from '../../../Components/PopupConfirmations';
import { useAuthContext } from '../../../Context/AuthContext';

const AddQuestion = ({ onClose }) => {
    const { authState } = useAuthContext();
    const [category, setCategory] = useState('');
    const [question, setQuestion] = useState('');
    const [option1, setOption1] = useState('');
    const [option2, setOption2] = useState('');
    const [option3, setOption3] = useState('');
    const [option4, setOption4] = useState('');
    const [answer, setAnswer] = useState('');
    const [questionFile, setQuestionFile] = useState(null);

    const handleFileChange = (event) => {
        setQuestionFile(event.target.files[0]);
    };

    const handleAdd = async () => {
        const formData = new FormData();
        formData.append('category', category);
        formData.append('question', question);
        formData.append('option1', option1);
        formData.append('option2', option2);
        formData.append('option3', option3);
        formData.append('option4', option4);
        formData.append('answer', answer);

        if (questionFile) {
            formData.append('questionFile', questionFile);
        }

        try {
            const response = await fetch(`https://localhost:5201/Question/Create`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${authState.token}`,
                },
                body: formData,
            });

            if (response.ok) {
                 await showSuccessNotification("Question added successfully"); 
                onClose();
            } else {
                console.error('Failed to add', response.statusText);
            }
        } catch (error) {
            console.error('Error:', error);
        }
    };

    return (
        <div className="p-6 max-w-xl mx-auto bg-gray-100 shadow-lg rounded-lg">
            <h2 className="my-4 text-2xl font-bold text-center text-gray-900">Add Question</h2>

            {/* Category Input */}
            <div className="mb-6">
                <label className="block mb-2 text-sm font-semibold text-gray-900">Category</label>
                <input
                    type="text"
                    value={category}
                    onChange={(e) => setCategory(e.target.value)}
                    className="w-full bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg p-2.5 focus:ring-blue-500 focus:border-blue-500"
                    placeholder="Enter Category"
                />
            </div>

            {/* Question Input */}
            <div className="mb-6">
                <label className="block mb-2 text-sm font-semibold text-gray-900">Question</label>
                <input
                    type="text"
                    value={question}
                    onChange={(e) => setQuestion(e.target.value)}
                    className="w-full bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg p-2.5 focus:ring-blue-500 focus:border-blue-500"
                    placeholder="Enter Question"
                />
            </div>

            {/* File Input */}
            <div className="mb-6">
                <label className="block mb-2 text-sm font-semibold text-gray-900">Image (if needed)</label>
                <input
                    type="file"
                    onChange={handleFileChange}
                    className="w-full text-sm text-gray-900 border border-gray-300 rounded-lg p-2.5 focus:ring-blue-500 focus:border-blue-500"
                />
            </div>

            {/* Option Inputs */}
            <div className="mb-6">
                <label className="block mb-2 text-sm font-semibold text-gray-900">Option 1</label>
                <input
                    type="text"
                    value={option1}
                    onChange={(e) => setOption1(e.target.value)}
                    className="w-full bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg p-2.5 focus:ring-blue-500 focus:border-blue-500"
                    placeholder="Enter Option 1"
                />
            </div>

            <div className="mb-6">
                <label className="block mb-2 text-sm font-semibold text-gray-900">Option 2</label>
                <input
                    type="text"
                    value={option2}
                    onChange={(e) => setOption2(e.target.value)}
                    className="w-full bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg p-2.5 focus:ring-blue-500 focus:border-blue-500"
                    placeholder="Enter Option 2"
                />
            </div>

            <div className="mb-6">
                <label className="block mb-2 text-sm font-semibold text-gray-900">Option 3</label>
                <input
                    type="text"
                    value={option3}
                    onChange={(e) => setOption3(e.target.value)}
                    className="w-full bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg p-2.5 focus:ring-blue-500 focus:border-blue-500"
                    placeholder="Enter Option 3"
                />
            </div>

            <div className="mb-6">
                <label className="block mb-2 text-sm font-semibold text-gray-900">Option 4</label>
                <input
                    type="text"
                    value={option4}
                    onChange={(e) => setOption4(e.target.value)}
                    className="w-full bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg p-2.5 focus:ring-blue-500 focus:border-blue-500"
                    placeholder="Enter Option 4"
                />
            </div>

            {/* Answer Input */}
            <div className="mb-6">
                <label className="block mb-2 text-sm font-semibold text-gray-900">Answer</label>
                <input
                    type="text"
                    value={answer}
                    onChange={(e) => setAnswer(e.target.value)}
                    className="w-full bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg p-2.5 focus:ring-blue-500 focus:border-blue-500"
                    placeholder="Enter Answer"
                />
            </div>

            {/* Buttons */}
            <div className="flex justify-between space-x-2">
                <button
                    onClick={() => { handleAdd(); onClose(); }}
                    className="w-1/2 text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5"
                >
                    Add Question
                </button>
                <button
                    onClick={onClose}
                    className="w-1/2 text-white bg-red-500 hover:bg-red-700 focus:ring-4 focus:outline-none focus:ring-red-300 font-medium rounded-lg text-sm px-5 py-2.5"
                >
                    Close
                </button>
            </div>
        </div>
    );
};

export default AddQuestion;
