import React, { useState, useEffect } from 'react';
import '../../assets/styles/register.css';
import { showSuccessNotification } from "../../Components/PopupConfirmations";
import { useNavigate } from 'react-router-dom';
import { useAuthContext } from '../../Context/AuthContext';

const Register = () => {
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    confirmPassword: '',
    firstname: '',
    lastname: '',
    middlename: '',
    phonenumber: '',
    pannumber: '',
    address: '',
    city: '',
    state: '',
    country: '',
    zipcode: '',
  });

  const [candidateDetails, setCandidateDetails] = useState(false);
  const [error, setError] = useState('');
  const [isPasswordVisible, setPasswordVisible] = useState(false);
  const [isConfirmPasswordVisible, setConfirmPasswordVisible] = useState(false);
  const navigateTo = useNavigate();
  const { authState, loginWithAuthContext } = useAuthContext();

  // Handle form changes
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  // Toggle visibility for password inputs
  const togglePasswordVisibility = () => setPasswordVisible(!isPasswordVisible);
  const toggleConfirmPasswordVisibility = () => setConfirmPasswordVisible(!isConfirmPasswordVisible);

  // Handle Register
  const handleRegister = async (e) => {
    e.preventDefault();
    setError('');

    const { email, password, confirmPassword } = formData;

    // Check if passwords match
    if (password !== confirmPassword) {
      setError('Passwords do not match.');
      return;
    }

    try {
      // Check if the email is already taken
      const emailCheckResponse = await fetch(
        `https://localhost:5201/api/Account/emailtaken?email=${email}`,
        { method: 'GET' }
      );
      if (emailCheckResponse.ok) {
        const isEmailTaken = await emailCheckResponse.json();
        if (isEmailTaken) {
          setError(`The email "${email}" is already taken.`);
          return;
        }
      }

      // Prepare candidate data
      const candidateData = {
        email: formData.email,
        password: formData.password,
        candidate: {
          createdBy_UserId: "",
          updatedBy_UserId: "",
          firstName: formData.firstname,
          lastName: formData.lastname,
          middleName: formData.middlename,
          emailAddress: formData.email,
          mobileNumber: formData.phonenumber,
          pan: formData.pannumber,
          active: true,
          permanentAddress: {
            firstName: formData.firstname,
            lastName: formData.lastname,
            addressLine: formData.address,
            city: formData.city,
            state: formData.state,
            zipCode: formData.zipcode,
            country: formData.country,
          },
          currentAddress: {
            firstName: formData.firstname,
            lastName: formData.lastname,
            addressLine: formData.address,
            city: formData.city,
            state: formData.state,
            zipCode: formData.zipcode,
            country: formData.country,
          },
        },
      };

      const registerResponse = await fetch('https://localhost:5201/api/Account/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(candidateData),
      });

      if (registerResponse.ok) {
        const data = await registerResponse.json();
        loginWithAuthContext(data.token, data.userId, data.email, data.role);
        await showSuccessNotification("Registration successful!");
        navigateTo('/home');
      } else {
        const data = await registerResponse.json();
        setError(data.message || 'Registration failed');
      }
    } catch (error) {
      console.error('Error during registration:', error);
      setError('Registration failed. Please try again.');
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-100">
      <div className="w-full max-w-2xl p-12 space-y-8 bg-white rounded-lg shadow-lg">
        <div className="text-center">
          <h2 className="text-4xl font-bold text-gray-900">
            {candidateDetails ? 'Add Candidate Details' : 'Register'}
          </h2>
        </div>

        {error && (
          <div className="text-center text-sm text-red-500 bg-red-100 py-3 rounded">
            {error}
          </div>
        )}

        <form className="space-y-6" onSubmit={handleRegister}>
          {/* Shared Form Elements */}
          <div>
            <label className="elements">Email address</label>
            <input
              name="email"
              type="email"
              value={formData.email}
              onChange={handleChange}
              required
              className="mt-3 custom-class"
            />
          </div>
          {/* Password */}
          <div className="relative">
            <label className="elements">Password</label>
            <input
              id="password"
              name="password"
              type={isPasswordVisible ? 'text' : 'password'}
              value={formData.password}
              onChange={handleChange}
              required
              className="mt-3 custom-class"
            />
            <button
              type="button"
              onClick={togglePasswordVisibility}
              className="absolute top-1/2 right-3 transform -translate-y-1/2 pt-7 text-gray-500 hover:text-gray-700"
            >
              {isPasswordVisible ? 'Hide' : 'Show'}
            </button>
          </div>

          {/* Confirm Password */}
          <div className="relative">
            <label className="elements">Confirm Password</label>
            <input
              id="confirmPassword"
              name="confirmPassword"
              type={isConfirmPasswordVisible ? 'text' : 'password'}
              value={formData.confirmPassword}
              onChange={handleChange}
              required
              className="mt-3 custom-class"
            />
            <button
              type="button"
              onClick={toggleConfirmPasswordVisibility}
              className="absolute top-1/2 right-3 transform -translate-y-1/2 pt-7 text-gray-500 hover:text-gray-700"
            >
              {isConfirmPasswordVisible ? 'Hide' : 'Show'}
            </button>
          </div>

          {/* Form Fields */}
          <div className="grid grid-cols-1 gap-4 sm:grid-cols-3">
            <div>
              <label className="elements">First Name</label>
              <input
                name="firstname"
                value={formData.firstname}
                onChange={handleChange}
                required
                className="mt-1 custom-class"
              />
            </div>
            <div>
              <label className="elements">Middle Name</label>
              <input
                name="middlename"
                value={formData.middlename}
                onChange={handleChange}
                className="mt-1 custom-class"
              />
            </div>
            <div>
              <label className="elements">Last Name</label>
              <input
                name="lastname"
                value={formData.lastname}
                onChange={handleChange}
                required
                className="mt-1 custom-class"
              />
            </div>
          </div>

          {/* Other Inputs */}
          <div>
            <label className="elements">Phone Number</label>
            <input
              name="phonenumber"
              value={formData.phonenumber}
              onChange={handleChange}
              required
              className="mt-1 custom-class"
            />
          </div>
          <div>
            <label className="elements">PAN Number</label>
            <input
              name="pannumber"
              value={formData.pannumber}
              onChange={handleChange}
              className="mt-1 custom-class"
            />
          </div>

          {/* Address Details */}
          <div>
            <label className="elements">Address</label>
            <textarea
              name="address"
              value={formData.address}
              onChange={handleChange}
              required
              className="mt-1 custom-class"
            />
          </div>

          {/* Address Details Continued */}
          <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
            <div>
              <label className="elements">City</label>
              <input
                name="city"
                value={formData.city}
                onChange={handleChange}
                required
                className="mt-1 custom-class"
              />
            </div>
            <div>
              <label className="elements">State</label>
              <input
                name="state"
                value={formData.state}
                onChange={handleChange}
                required
                className="mt-1 custom-class"
              />
            </div>
          </div>
          <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
            <div>
              <label className="elements">Country</label>
              <input
                name="country"
                value={formData.country}
                onChange={handleChange}
                required
                className="mt-1 custom-class"
              />
            </div>
            <div>
              <label className="elements">Zip Code</label>
              <input
                name="zipcode"
                value={formData.zipcode}
                onChange={handleChange}
                required
                className="mt-1 custom-class"
              />
            </div>
          </div>

          {/* Submit Button */}
          <div>
            <button
              type="submit"
              className="w-full py-3 px-6 text-base font-semibold text-white bg-indigo-600 hover:bg-indigo-700 rounded-lg shadow focus:ring-2 focus:ring-indigo-500 focus:outline-none"
            >
              {candidateDetails ? 'Add Candidate' : 'Register'}
            </button>
          </div>
        </form>

        <div className="text-center">
          <p className="text-sm text-gray-600">
            Already have an account?{' '}
            <button
              type="button"
              className="text-indigo-600 hover:text-indigo-800 focus:outline-none"
            >
              Sign in
            </button>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Register;
