import React, { useState } from 'react';
import { showSuccessNotification, showErrorNotification } from '../../../Components/PopupConfirmations';
import { useAuthContext } from '../../../Context/AuthContext';

const AddPaperset = ({ onClose }) => {
  const { authState } = useAuthContext(); // Access token and user information from AuthContext
  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [active, setActive] = useState(true);
  const [time, setTime] = useState('');

  const handleAdd = async () => {
    const newPaperSet = {
      name,
      description,
      active,
      timeInMinutes: parseInt(time, 10), // Ensure time is sent as a number
      createdBy_UserId: authState?.userId || '', // Use userId from authState if available
      updatedBy_UserId: authState?.userId || '', // Use userId from authState if available
    };

    try {
      const response = await fetch('https://localhost:5201/PaperSet/Create', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${authState.token}`, // Attach token from context
        },
        body: JSON.stringify(newPaperSet),
      });

      if (response.ok) {
        await showSuccessNotification('Paperset added successfully!');
        onClose(); // Close the modal or popup
      } else {
        const errorResponse = await response.json();
        await showErrorNotification(`Failed to add paperset: ${errorResponse.message || 'Unknown error'}`);
      }
    } catch (error) {
      console.error('Error adding paperset:', error);
      await showErrorNotification('An error occurred while adding the paperset.');
    }
  };

  return (
    <div className="p-6 max-w-xl mx-auto bg-gray-100 shadow-lg rounded-lg">
      <h2 className="my-4 text-2xl font-bold text-center text-gray-900">Add Paperset</h2>

      {/* Name Input */}
      <div className="mb-6">
        <label className="block mb-2 text-sm font-semibold text-gray-900">Name</label>
        <input
          type="text"
          value={name}
          onChange={(e) => setName(e.target.value)}
          className="w-full bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg p-2.5 focus:ring-blue-500 focus:border-blue-500"
          placeholder="Enter Paperset Name"
        />
      </div>

      {/* Description Input */}
      <div className="mb-6">
        <label className="block mb-2 text-sm font-semibold text-gray-900">Description</label>
        <input
          type="text"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          className="w-full bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg p-2.5 focus:ring-blue-500 focus:border-blue-500"
          placeholder="Enter Paperset Description"
        />
      </div>

      {/* Active Status */}
      <div className="mb-6">
        <label className="block mb-2 text-sm font-semibold text-gray-900">Active</label>
        <select
          value={active}
          onChange={(e) => setActive(e.target.value === 'true')}
          className="w-full bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg p-2.5 focus:ring-blue-500 focus:border-blue-500"
        >
          <option value="true">True</option>
          <option value="false">False</option>
        </select>
      </div>

      {/* Duration (Minutes) */}
      <div className="mb-6">
        <label className="block mb-2 text-sm font-semibold text-gray-900">Duration (Minutes)</label>
        <input
          type="number"
          value={time}
          onChange={(e) => setTime(e.target.value)}
          className="w-full bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg p-2.5 focus:ring-blue-500 focus:border-blue-500"
          placeholder="Enter Duration in Minutes"
        />
      </div>

      {/* Buttons */}
      <div className="flex justify-between space-x-2">
        <button
          onClick={handleAdd}
          className="w-1/2 text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5"
        >
          Add Paperset
        </button>
        <button
          onClick={onClose}
          className="w-1/2 text-white bg-red-500 hover:bg-red-700 focus:ring-4 focus:outline-none focus:ring-red-300 font-medium rounded-lg text-sm px-5 py-2.5"
        >
          Close
        </button>
      </div>
    </div>
  );
};

export default AddPaperset;
