import React, { createContext, useState, useContext, useEffect } from 'react';

const AuthContext = createContext();  // Create an AuthContext to share auth status globally

// AuthProvider to provide auth state to the app
export const AuthProvider = ({ children }) => {
  const [authState, setAuthState] = useState({
    isAuthenticated: false,
    token: '',
    userId: '',
    email: '',
    role: '',
    loginDate: '',
  });

  // Check for authentication data in localStorage on mount
  useEffect(() => {
    const storedAuthState = JSON.parse(localStorage.getItem('authState'));
    if (storedAuthState && storedAuthState.token) {
      setAuthState(storedAuthState);
    }
  }, []);

  const loginWithAuthContext = (token, userId, email, role) => {
    const loginDate = new Date().toISOString();
    const authData = { token: token, userId: userId, email: email, role: role, loginDate: loginDate, isAuthenticated: true };
    localStorage.setItem('authState', JSON.stringify(authData));
    setAuthState(authData);
  };

  const logoutWithAuthContext = () => {
    setAuthState({
      isAuthenticated: false,
      token: '',
      userId: '',
      email: '',
      role: '',
      loginDate: '',
    });
    localStorage.removeItem('authState');
  };

  return (
    <AuthContext.Provider value={{ authState, loginWithAuthContext, logoutWithAuthContext }}>
      {children}
    </AuthContext.Provider>
  );
};

// Custom hook to use auth context
export const useAuthContext = () => useContext(AuthContext);
