import React, { useState, useEffect } from 'react';
import { useAuthContext } from '../../../Context/AuthContext';  
import UpdateQuestion from './UpdateQuestion';
import AddQuestion from './AddQuestion';
import { showDeleteConfirmation } from '../../../Components/PopupConfirmations';

const Question = () => {
  const [questions, setQuestions] = useState([]);
  const [selectedItemId, setSelectedItemId] = useState(null);
  const [selectedName, setSelectedName] = useState(null);
  const [isCurrentContentVisible, setCurrentContentVisibility] = useState(true);
  const [isView, setView] = useState(false);
  const [isPopupOpenForAdd, setPopupOpenForAdd] = useState(false);
  const [isDelete, setIsDelete] = useState(false);
  const { authState } = useAuthContext();
  let count = 0;

  const handleOpenPopupForAdd = () => {
    setPopupOpenForAdd(true);
    setCurrentContentVisibility(false);
  };

  const handleClosePopupForAdd = () => {
    setPopupOpenForAdd(false);
    setCurrentContentVisibility(true);
  };

  const handleOpenView = (id, name) => {
    setView(true);
    setSelectedItemId(id);
    setSelectedName(name);
    setCurrentContentVisibility(false);
  };

  const handleCloseView = () => {
    setView(false);
    setSelectedItemId(null);
    setSelectedName(null);
    setCurrentContentVisibility(true);
  };

  const handleDelete = async (id) => {
    const isConfirmed = await showDeleteConfirmation();

    if (isConfirmed) {
      try {
        const response = await fetch(`https://localhost:5201/Question/Delete?Id=${id}`, {
          method: 'DELETE',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${authState.token}`,
          },
        });

        if (response.ok) {
          setIsDelete(true);
          setTimeout(() => {
            setIsDelete(false);
          }, 2000);
        } else {
          console.error('Failed to delete data:', response.statusText);
        }
      } catch (error) {
        console.error('Error deleting data:', error);
      }
    }
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('https://localhost:5201/Question/GetAll', {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${authState.token}`,
          },
        });

        if (response.ok) {
          const data = await response.json();
          setQuestions(data);
        } else {
          console.error('Failed to fetch data:', response.statusText);
        }
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    if (authState.token) {
      fetchData();
    }
  }, [authState.token]);

  return (
    <div className="p-6 min-h-screen">
      {isCurrentContentVisible && (
        <div>
          {isDelete && (
            <div className="bg-teal-100 border-t-4 border-teal-500 rounded-b text-teal-900 px-4 py-3 shadow-md mb-4" role="alert">
              <div className="flex">
                <div className="py-1">
                  <svg
                    className="fill-current h-6 w-6 text-teal-500 mr-4"
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 20 20"
                  >
                    <path d="M2.93 17.07A10 10 0 1 1 17.07 2.93 10 10 0 0 1 2.93 17.07zm12.73-1.41A8 8 0 1 0 4.34 4.34a8 8 0 0 0 11.32 11.32zM9 11V9h2v6H9v-4zm0-6h2v2H9V5z" />
                  </svg>
                </div>
                <div>
                  <p className="font-bold">Deleted successfully!</p>
                  <p className="text-sm">Selected question deleted</p>
                </div>
              </div>
            </div>
          )}

          <h2 className="text-center text-3xl font-bold leading-9 mb-6 underline text-gray-700">Questions</h2>
          <div className="my-4 flex justify-end">
            <button
              className="px-4 py-2 bg-blue-500 hover:bg-blue-700 text-white font-semibold rounded shadow-md transition-all duration-300"
              onClick={handleOpenPopupForAdd}
            >
              + Add Question
            </button>
          </div>
          <div className="relative overflow-x-auto rounded-lg shadow-md">
            <table className="w-full text-sm text-center text-gray-700 bg-white">
              <thead className="text-lg font-semibold bg-gray-200 text-black">
                <tr>
                  <th className="px-6 py-4">Sr no</th>
                  <th className="px-6 py-4">Question</th>
                  <th className="px-6 py-4">Category</th>
                  <th className="px-6 py-4">Active</th>
                  <th className="px-6 py-4">Actions</th>
                </tr>
              </thead>
              <tbody>
                {questions.map((item) => (
                  <tr
                    className="border-b hover:bg-gray-100 transition-all duration-200"
                    key={item.id}
                  >
                    <td className="px-6 py-4">{++count}</td>
                    <td className="px-6 py-4">{item.questionText}</td>
                    <td className="px-6 py-4">{item.category}</td>
                    <td className="px-6 py-4">{item.active ? 'Yes' : 'No'}</td>
                    <td className="px-6 py-4 flex justify-center space-x-2">
                      <button
                        className="px-4 py-2 bg-blue-500 hover:bg-blue-700 text-white font-semibold rounded shadow"
                        onClick={() => handleOpenView(item.id, item.question)}
                      >
                        Update
                      </button>
                      <button
                        className="px-4 py-2 bg-red-500 hover:bg-red-700 text-white font-semibold rounded shadow"
                        onClick={() => handleDelete(item.id)}
                      >
                        Delete
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}

      {isView && <UpdateQuestion id={selectedItemId} name={selectedName} token={authState.token} onClose={handleCloseView} />}

      {isPopupOpenForAdd && <AddQuestion token={authState.token} onClose={handleClosePopupForAdd} />}
    </div>
  );
};

export default Question;
