import React from 'react';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';
import { AuthProvider, useAuthContext } from './Context/AuthContext';
import Navbar from './Components/Navbar';
import { Login, Register, ForgotPassword } from './Pages/Account';
import { AdminHomePage, PaperSetList, CandidateList, Question, UpdateQuestion, Status } from './Pages/Admin';
import { CandidateHomePage, CandidateProfile } from './Pages/Candidate';
import { AboutPage, ErrorPage } from './Pages/Shared';
import './App.css';

function App() {
  const { authState } = useAuthContext();

  return (
    <div>
      <Navbar />

      <Routes>
        {/* Account Routes */}
        <Route path="/login" element={!authState.isAuthenticated ? <Login /> : <Navigate to="/home" />} />
        <Route path="/register" element={!authState.isAuthenticated ? <Register /> : <Navigate to="/home" />} />
        <Route path="/forgotPassword" element={!authState.isAuthenticated ? <ForgotPassword /> : <Navigate to="/home" />} />

        {/* Protected Routes - Home Page */}
        <Route path="/" element={!authState.isAuthenticated ? <Navigate to="/login" /> : <Navigate to="/home" />} />
        <Route path="/home" element={!authState.isAuthenticated ? <Navigate to="/login" /> : (authState.role.toLowerCase() === "admin" ? <AdminHomePage /> : <CandidateHomePage />)} />

        {/* Protected Routes - Admin */}
        <Route path="/paperSetList" element={!authState.isAuthenticated ? <Navigate to="/login" /> : <PaperSetList />} />
        <Route path="/candidateList" element={!authState.isAuthenticated ? <Navigate to="/login" /> : <CandidateList />} />
        <Route path="/question" element={!authState.isAuthenticated ? <Navigate to="/login" /> : <Question />} />
        <Route path="/updateQuestion/:id" element={!authState.isAuthenticated ? <Navigate to="/login" /> : <UpdateQuestion />} />
        <Route path="/status" element={!authState.isAuthenticated ? <Navigate to="/login" /> : <Status />} />

        {/* Protected Routes - Candidate */}
        <Route path="/candidateProfile" element={!authState.isAuthenticated ? <Navigate to="/login" /> : <CandidateProfile />} />

        {/* Shared Routes */}
        <Route path="/about" element={<AboutPage />} />

        {/* Fallback Error Route */}
        <Route path="*" element={<ErrorPage />} />
      </Routes>
    </div>
  );
}

// Wrap your App with AuthProvider
const authApp = () => (
  <AuthProvider>
    <BrowserRouter>
      <App />
    </BrowserRouter>
  </AuthProvider>
);

export default authApp;
