import React, { useState, useEffect } from 'react';
import { useAuthContext } from '../../../Context/AuthContext';

const ShowByStaus = ({ status, onClose }) => {
    const { authState } = useAuthContext(); // Using authState from AuthContext
    const [candidate, setCandidate] = useState([]);
    let count = 0;

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch(`https://localhost:5201/CandidateStatus/Result?result=${status}`, {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Bearer ${authState.token}`, // Using token from context
                    },
                });

                if (response.ok) {
                    const data = await response.json();
                    console.log('Data Fetched');
                    setCandidate(data);
                } else {
                    console.error('Failed to fetch data:', response.statusText);
                }
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        fetchData();
    }, [status, authState.token]); // Added authState.token to dependency array

    return (
        <div>
            <h2 className="mt-3 text-center text-2xl font-bold leading-9 mb-3 underline">Candidate list by result</h2>
            <div className="relative overflow-x-auto">
                <table className="w-full text-sm text-center rtl:text-right text-black dark:text-gray-400 border-b-4 border-gray-700">
                    <thead className="text-lg tracking-wide text-gray-700 bg-gray-500 dark:bg-gray-700 dark:text-gray-400">
                        <tr className="rounded-md bg-gradient-to-r from-green-300 via-blue-500 to-purple-600 bg-clip-bg text-white">
                            <th scope="col" className="px-6 py-3 font-extrabold">Sr no</th>
                            <th scope="col" className="px-6 py-3 font-extrabold">Candidate Name</th>
                            <th scope="col" className="px-6 py-3 font-extrabold">Email</th>
                            <th scope="col" className="px-6 py-3">Status</th>
                            <th scope="col" className="px-6 py-3">Result</th>
                            <th scope="col" className="px-6 py-3">Submited On</th>
                        </tr>
                    </thead>
                    <tbody className="bg-white border-b dark:bg-gray-800 dark:border-gray-700">
                        {candidate.map((item) => (
                            <tr
                                className="rounded-md bg-gradient-to-r from-green-100 via-blue-300 to-purple-400 bg-clip-bg text-black font-bold dark:border-gray-700"
                                key={item.id}
                            >
                                <td className="px-6 py-4">{++count}</td>
                                <td className="px-6 py-4">{item.candidateName}</td>
                                <td className="px-6 py-4">{item.email}</td>
                                <td className="px-6 py-4">{item.status ? 'Pass' : 'Fail'}</td>
                                <td className="px-6 py-4">{item.result}%</td>
                                <td className="px-6 py-4">
                                    {new Date(item.submitedOn).toLocaleDateString('en-GB', {
                                        day: '2-digit',
                                        month: '2-digit',
                                        year: 'numeric',
                                    })}
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
            <div className="flex justify-end content-center mt-1">
                <button
                    className="ml-1 px-2 py-2 w-20 bg-red-500 hover:bg-red-700 text-white font-bold border border-red-700 rounded"
                    onClick={onClose}
                >
                    Close
                </button>
            </div>
        </div>
    );
};

export default ShowByStaus;
